<template>
	<div>
		<div v-if="pk==0">訂單編號：新訂單</div>
		<div v-else>訂單編號：{{ pk }}</div>
		<div v-if="mode=='read'" class="paddTopBtmSm">
			<div class="bareBtnCtnr">
				<button
					@click="mode='write'"
					class="btn btn-primary"
				><font-awesome-icon icon="pen" class="textAside" />編輯</button>
			</div>
			<div>建立日期：{{ order.create_date }}</div>
			<div>姓名：{{ name }}</div>
			<div>地址：{{ address }}</div>
			<div>郵遞區號：{{ zip }}</div>
			<div>公司單/位：{{ company }}</div>
			<div>聯絡電話：{{ phone }}</div>
			<div>性別：
				<template v-if="sex==='man'">
					男
				</template>
				<template v-if="sex==='woman'">
					女
				</template>
			</div>
			<div>捐款：
				<template v-if="receipt=='true'">否</template>
				<template v-else>是</template>
			</div>
			<div
				v-if="receipt=='true'"
			>發票捐贈：
				<template v-if="receipt_donation">是</template>
				<template v-else>否</template>
			</div>
			<template
				v-if="receipt_donation===false && receipt=='true'"
			>
				<div>發票統編：{{ receipt_number }}</div>
				<div>發票抬頭：{{ receipt_title }}</div>
			</template>
			<div
				v-if="receipt=='false'"
			>身份證字號或統編：{{ id_number }}</div>
			<div>付款方式：{{ payment_category }}</div>
			<div>付款資訊：{{ payment_info }}</div>
			<div>備註：{{ remark }}</div>
			<div>狀態：
				<template v-if="status==0">
					資料已建立
					<font-awesome-icon icon="chevron-right" class="textAside" />
					<button
						@click="next"
						class="btn btn-primary"
					>資料已確認</button>
				</template>
				<template v-else-if="status==1">
					資料已確認
					<font-awesome-icon icon="chevron-right" class="textAside" />
					<button
						@click="next"
						class="btn btn-primary"
					>發票或收據已開立</button>
				</template>
				<template v-else-if="status==2">
					發票或收據已開立
				</template>
			</div>
		</div>
		<div v-if="mode=='write'" class="paddTopBtmSm">
			<div class="bareBtnCtnr">
				<button
					@click="cancel"
					class="btn btn-secondary"
				><font-awesome-icon icon="times" class="textAside" />取消</button>
			</div>
			<formdrf
				:model_info="model_info.create_date"
				:field="'create_date'"
				:offset-class="'col-sm-offset-3'"
				v-model="create_date"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.name"
				:field="'name'"
				:offset-class="'col-sm-offset-3'"
				v-model="name"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.address"
				:field="'address'"
				:offset-class="'col-sm-offset-3'"
				v-model="address"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.zip"
				:field="'zip'"
				:offset-class="'col-sm-offset-3'"
				v-model="zip"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.company"
				:field="'company'"
				:offset-class="'col-sm-offset-3'"
				v-model="company"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.phone"
				:field="'phone'"
				:offset-class="'col-sm-offset-3'"
				v-model="phone"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.sex"
				:field="'sex'"
				:offset-class="'col-sm-offset-3'"
				v-model="sex"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.source"
				:field="'source'"
				:offset-class="'col-sm-offset-3'"
				v-model="source"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.receipt"
				:field="'receipt'"
				:offset-class="'col-sm-offset-3'"
				v-model="receipt"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				v-if="receipt=='true'"
				:model_info="model_info.receipt_donation"
				:field="'receipt_donation'"
				:offset-class="'col-sm-offset-3'"
				v-model="receipt_donation"
				@keyup.enter.native=""
			></formdrf>
			<template
				v-if="receipt_donation===false && receipt=='true'"
			>
				<formdrf
					:model_info="model_info.receipt_number"
					:field="'receipt_number'"
					:offset-class="'col-sm-offset-3'"
					v-model="receipt_number"
					@keyup.enter.native=""
				></formdrf>
				<formdrf
					:model_info="model_info.receipt_title"
					:field="'receipt_title'"
					:offset-class="'col-sm-offset-3'"
					v-model="receipt_title"
					@keyup.enter.native=""
				></formdrf>
			</template>
			<formdrf
				v-if="receipt=='false'"
				:model_info="model_info.id_number"
				:field="'id_number'"
				:offset-class="'col-sm-offset-3'"
				v-model="id_number"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.payment_category"
				:field="'payment_category'"
				:offset-class="'col-sm-offset-3'"
				v-model="payment_category"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.payment_info"
				:field="'payment_info'"
				:offset-class="'col-sm-offset-3'"
				v-model="payment_info"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.remark"
				:field="'remark'"
				:offset-class="'col-sm-offset-3'"
				v-model="remark"
				@keyup.enter.native=""
			></formdrf>
			<div v-if="pk===0">
				<!--<button @click="operate('create')">建立</button>-->
			</div>
			<div v-else>
				<div class="bareBtnCtnr">
					<button
						@click="operate('update')"
						class="btn btn-primary"
					><font-awesome-icon icon="sync-alt" class="textAside" />儲存</button>
					<button
						@click="operate('delete')"
						class="btn btn-secondary"
					><font-awesome-icon icon="trash-alt" class="textAside" />刪除</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import formdrf from './form.vue';
import { order_rest as orderRest, sourceDMRest, } from '@/api/sale';

export default {
	props: {
		pk: {
			type: Number,
			default: 0,
		},
	},
	components: {
		formdrf: formdrf,
	},
	data() {
		return {
			id: -Math.floor(Math.random() * 100000000).toString(),
			mode: 'read',
			order: {
				id: 0,
				status: 0,
				name: null,
				address: null,
				zip: null,
				phone: null,
				company: null,
				sex: 'man',
				source: '',
				create_date: null,
				receipt: 'true',
				receipt_donation: true,
				receipt_number: '',
				receipt_title: '',
				id_number: '',
				payment_category: '信用卡',
				payment_info: '',
				remark: '',
			}, // API 取得 order
			status: 0,
			name: null,
			address: null,
			zip: null,
			phone: null,
			company: null,
			sex: 'man',
			source: '',
			create_date: null,
			receipt: 'true',
			receipt_donation: true,
			receipt_number: '',
			receipt_title: '',
			id_number: '',
			payment_category: '信用卡',
			payment_info: '',
			remark: '',
			model_info: {
				create_date: {
					'label': '訂單建立日期',
					'type': 'text',
				},
				name: {
					'label': '姓名',
					'type': 'text',
					required: true,
				},
				address: {
					'label': '地址',
					'type': 'text',
					required: true,
				},
				zip: {
					'label': '郵地區號',
					'type': 'text',
					required: true,
				},
				company: {
					'label': '公司/單位',
					'type': 'text',
				},
				phone: {
					'label': '聯絡電話',
					'type': 'text',
					required: true,
				},
				sex: {
					'label': '性別',
					'type': 'select',
						'choices' : [
							{
								'value': 'man',
								'display_name': '男',
							},
							{
								'value': 'woman',
								'display_name': '女',
							},
						],
				},
				source: {
					'label': '來源(DM)',
					'type': 'select',
				},
				receipt: {
					'label': '類別',
					'type': 'select',
						'choices' : [
							{
								'value': '',
								'display_name': '---------',
							},
							{
								'value': false,
								'display_name': '捐贈',
							},
							{
								'value': true,
								'display_name': '購買',
							},
						],
				},
				receipt_donation: {
					'label': '發票捐贈',
					'type': 'checkbox',
				},
				receipt_number: {
					'label': '發票統編',
					'type': 'text',
				},
				receipt_title: {
					'label': '發票抬頭',
					'type': 'text',
				},
				id_number: {
					'label': '身份證字號或統編',
					'type': 'text',
				},
				payment_category: {
					'label': '付款方式',
					'type': 'select',
					'choices' : [
						{
							'value': '信用卡',
							'display_name': '信用卡',
						},
						{
							'value': '轉帳',
							'display_name': '轉帳',
						},
						{
							'value': '郵政劃撥',
							'display_name': '郵政劃撥',
						},
						],
				},
				payment_info: {
					'label': '付款資訊',
					'type': 'text',
				},
				remark: {
					'label': '備註',
					'type': 'text',
				},
			},
		};
	},
	computed: {
		orderUpdate(){
			return {
				"id": this.pk,
				status: this.status,
				name: this.name,
				address: this.address,
				zip: this.zip,
				phone: this.phone,
				company: this.company,
				sex: this.sex,
				source: this.source,
			create_date: this.create_date,
				receipt: this.receipt,
				receipt_donation: this.receipt_donation,
				receipt_number: this.receipt_number,
				receipt_title: this.receipt_title,
				id_number: this.id_number,
				payment_category: this.payment_category,
				payment_info: this.payment_info,
				remark: this.remark,
			}
		},
	},
	created(){
		this.$on('update', this.update_message);
		this.$on('delete', this.delete_message);
		this.$on('error', this.error_message);
	},
	async mounted() {
		await this.load();
	},
	methods: {
		async next(){
			this.status = this.status +1;
			let res;
			try {
				res = await orderRest.partialupdate(this.pk, {status: this.status});
				res = await orderRest.read(this.pk);
				this.order = res.data;
				this.order2data();
				this.$emit('update', res.data);
				return res;
			}
			catch (error) {
				this.$emit('error', error.response);
				throw(error.response);
			}
		},
		order2data(){
			this.pk = this.order.id;
			this.status = Number(this.order.status);
			this.name = this.order.name;
			this.address = this.order.address;
			this.zip = this.order.zip;
			this.phone = this.order.phone;
			this.company = this.order.company;
			this.sex = this.order.sex;
			this.source = this.order.source;
			this.create_date = this.order.create_date;
			this.receipt = String(this.order.receipt);
			this.receipt_donation = this.order.receipt_donation;
			this.receipt_number = this.order.receipt_number;
			this.receipt_title = this.order.receipt_title;
			this.id_number = this.order.id_number;
			this.payment_category = this.order.payment_category;
			this.payment_info = this.order.payment_info;
			this.remark = this.order.remark;
		},
		async load(){
			let res;
			try {
				res = await Promise.all([
					sourceDMRest.list(),
				])
				this.model_info.source.choices = [{
					value: '',
					display_name: '請選擇',
				}];
				res[0].data.forEach((v, i)=>{
					this.model_info.source.choices.push({
						value: v.name,
						display_name: v.name,
					})
				})
				if(this.pk){
					res = await orderRest.read(this.pk);
					this.order = res.data;
					this.mode = 'read'
				}
				else {
					this.mode = 'write'
				}
				this.order2data();
			}
			catch (error) {
				console.log(error);
			}
		},
		update_message(event){
			this.load();
			this.$root.$message.open(0, '成功儲存訂單' +event.id);
		},
		delete_message(event){
			this.load();
			this.$root.$message.open(0, '成功刪除訂單' +event.id);
		},
		error_message(event){
			this.$root.$message.open(event.status, event.data);
		},
		async operate(action){
			let res;
			if(action=='create'){
				try {
					res = await orderRest.create(this.orderUpdate);
					this.pk = res.data.id;
					res = await orderRest.read(this.pk);
					this.order = res.data;
					this.order2data();
					this.$emit('create', res.data);
					return res;
				}
				catch(error){
					this.$emit('error', error.response);
					throw(error.response);
				}
			}
			else if(action=='read'){
				try {
					res = await orderRest.read(this.pk)
					this.order = res.data;
					this.order2data();
				}
				catch (error) {
					console.log(JSON.stringify(error.response));
				}
			}
			else if(action=='update'){
				try {
					res = await orderRest.update(this.pk, this.orderUpdate);
					res = await orderRest.read(this.pk);
					this.order = res.data;
					this.order2data();
					this.$emit('update', res.data);
					return res;
				}
				catch (error) {
					this.$emit('error', error.response);
					throw(error.response);
				}
			}
			else if(action=='delete'){
				try {
					await this.$root.$confirm.open(0, '確定刪除此項目？');
					res = await orderRest.delete(this.pk)
					this.$emit('delete', {id: this.pk});
					return res;
				}
				catch (error) {
					this.$emit('error', error.response);
					throw(error.response);
				}
			}
		},
		cancel(){
			this.order2data();
			this.mode = 'read';
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';
</style>



