<template>
	<div class="pageCtnr">
		<com-header></com-header>

		<main role="main" class="paddLftRgtDefault paddTopBtmDefault">
			<router-view></router-view>
		</main>

		<com-footer></com-footer>
		<com-message></com-message>
		<com-confirm></com-confirm>
		<com-token></com-token>
	</div>
</template>

<script>
import comHeader from './header.vue';
import comFooter from './footer.vue';
import comMessage from './message.vue';
import comConfirm from './confirm.vue';
import comToken from './token.vue';

export default {
	name: 'sale',
	components: {
		comHeader,
		comFooter,
		comMessage,
		comConfirm,
		comToken,
	},
	data() {
		return {};
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';

	.pageCtnr {
		// .Used for sticky footer. see comment id 190513m140524.
		// ------------------------------------------------------
		display: grid;
		grid-template-rows: auto auto 1fr;
		// .Have to sync with html structure: For example, if your <footer> is the
		// 4th child of .pageCtnr, you change it to this -
		// "grid-template-rows: auto auto auto 1fr;".
		// .Purpose: Make <footer> occupying as much space as possible.
		width: 100%;
	}
	main {
		// .Used for sticky footer. see comment id 190513m140524.
		min-height: 30vh;
		// .Purpose: Fall back and fixing only for browser that don't support grid layout.
	}
</style>



