<template>
	<div>
		<div class="bareBtnCtnr">
			<button
				v-if="mode=='write'"
				@click="add"
				class="btn btn-primary"
			><font-awesome-icon icon="plus" class="textAside" />新增</button>
		</div>
		<table-div
			:header="productorder_header"
			:datas="productorder_datas"
		>
			<template slot="id" slot-scope="props">
				<div v-if="productorders[props.item].mode==='read'">
					{{ productorders[props.item].product }}
				</div>
				<div v-if="productorders[props.item].mode==='write'">
					<input v-model="productorders[props.item].product"/>
				</div>
			</template>
			<template slot="name" slot-scope="props">
				<div v-if="productorders[props.item].mode==='read'">
					<div v-for="item in product_list">
						<div v-if="item.id==productorders[props.item].product">
							{{ item.name }}
						</div>
					</div>
				</div>
				<div v-if="productorders[props.item].mode==='write'" class="input-group">
					<select v-model="productorders[props.item].product" class="custom-select">
						<option
							v-for="item in product_list"
							:value="item.id"
						>{{ item.name }}</option>
					</select>
				</div>
			</template>
			<template slot="count" slot-scope="props">
				<div v-if="productorders[props.item].mode==='read'">
					{{ productorders[props.item].count }}
				</div>
				<div v-if="productorders[props.item].mode==='write'">
					<input v-model="productorders[props.item].count"/>
				</div>
			</template>
			<template slot="action" slot-scope="props">
				<button
					v-if="productorders[props.item].mode==='read'"
					@click="productorders[props.item].mode='write'"
					class="btn btn-primary"
				><font-awesome-icon icon="pen" class="textAside" />編輯</button>
				<button
					v-if="productorders[props.item].mode==='write'"
					@click="productorders[props.item].mode='read'"
					class="btn btn-primary"
				><font-awesome-icon icon="check" class="textAside" />完成</button>
				<button
					@click="remove(props.item)"
					class="btn btn-secondary"
				><font-awesome-icon icon="times" class="textAside" />刪除</button>
			</template>
		</table-div>
		<div class="bareBtnCtnr">
			<template
				v-if="mode=='write'"
			>
				<button
					v-if="pk"
					@click="operate('update')"
					class="btn btn-primary"
				><font-awesome-icon icon="sync-alt" class="textAside" />更新</button>
			</template>
		</div>
	</div>
</template>

<script>
import {clone} from 'lodash';
import bsModal from './modal.vue';
import tableDiv from './table-div.vue';
import { order_rest as orderRest, product_rest as productRest, productorderRest } from '@/api/sale';

export default {
	props: {
		pk: {
			type: Number,
			default: 0,
		},
		mode: {
			type: String,
			default: 'write',
		},
	},
	name: 'order_productorders_manager',
	components: {
			bsModal,
		tableDiv,
	},
	data() {
		return {
			product_list: [], // API 商品列表
			order: {
				id: 0,
				productorder_set: [],
			}, // API 取得 order
			ids: [],
			productorders: [],
			productorder_header: {
				id: '代號',
				name: '名稱',
				count: '數量',
				action: '動作',
			},
		};
	},
	computed: {
		productorder_datas(){
			let temp = []
			this.productorders.forEach((v, i)=>  {
				temp.push({
					id: i,
					name: i,
					count: i,
					action: i,
				})
			})
			return temp;
		},
		productordersUpdate(){
			let temp = []
			let old_count = this.ids.length;
			let new_count = this.productorders.length
			let count = Math.max(new_count, old_count);
			for(let i=0; i<count; i++){
				if(new_count>old_count){
					if(i<old_count){
						temp.push({
							id: this.ids[i].id,
							order: this.pk,
							product: this.productorders[i].product,
							count: this.productorders[i].count,
							action: 'update',
						})
					}
					else 					if(i>=old_count){
						temp.push({
							id: null,
							order: this.pk,
							product: this.productorders[i].product,
							count: this.productorders[i].count,
							action: 'create',
						})
					}
				}
				else {
					if(i<new_count){
						temp.push({
							id: this.ids[i].id,
							order: this.pk,
							product: this.productorders[i].product,
							count: this.productorders[i].count,
							action: 'update',
						})
					}
					else 					if(i>=new_count){
						temp.push({
							id: this.ids[i].id,
							action: 'delete',
						})
					}
				}
			}
			return temp
		},
	},
	watch: {
	},
	async mounted() {
		await this.load();
	},
	methods: {
		async load(){
			let res;
			try {
				res = await Promise.all([
					productRest.list(),
				])
				this.product_list = res[0].data;
				if(this.pk){
					res = await orderRest.read(this.pk);
					this.order = res.data;
					await this.order2data();
				}
			}
			catch (error) {
				console.log(error);
			}
		},
		async order2data(){
			let res;
			res = await this.productorders_read_promise();
			this.productorders = [];
			this.ids = [];
			res.forEach((v, i)=>  {
				this.productorders.push({
					id: v.data.id,
					product: v.data.product,
					count: v.data.count,
					mode: 'read',
				});
			})
			res.forEach((v, i)=>  {
				this.ids.push({
					id: v.data.id,
				});
			})
		},
		add(){
			this.productorders.push({
				id: null,
				product: 1,
				count: 0,
				mode: 'write',
			});
		},
		remove(index){
			this.productorders.splice(index,1);
		},


		async operate(action){
			let res;
			if(action=='create'){
				try {
					res = await this.productorders_create_promise();
					res = await orderRest.read(this.pk);
					this.order = res.data;
					this.order2data();
				}
				catch (error) {
					console.log(JSON.stringify(error.response));
				}
			}
			else if(action=='read'){
				try {
					res = await orderRest.read(this.pk)
					this.order = res.data;
					this.order2data();
				}
				catch (error) {
					console.log(JSON.stringify(error.response));
				}
			}
			else if(action=='update'){
				try {
					res = await this.productorders_update_promise();
					res = await orderRest.read(this.pk);
					this.order = res.data;
					this.order2data();
					this.$emit('update', res.data);
					this.$root.$message.open(0, '成功更新商品資訊');
				}
				catch (error) {
					if(error.status){
						this.$root.$message.open(error.status, error.data);
					} else {
						console.log(error);
						this.$root.$message.open(-1, error);
					}
				}
			}

		},
		productorders_create_promise(){
			let axiosapis = [];
			this.productorders.forEach((v, i)=>  {
				v['id'] = null;
				v['order'] = this.pk;
				axiosapis.push(productorderRest.create(v));
			})
			return Promise.all(axiosapis);
		},
		productorders_read_promise(){
			let axiosapis = [];
			this.order.productorder_set.forEach((v, i)=>  {
				axiosapis.push(productorderRest.read(v));
			})
			return Promise.all(axiosapis);
		},
		productorders_update_promise(){
			let axiosapis = [];
			this.productordersUpdate.forEach((v, i)=>  {
				if(v.action==='create'){
					axiosapis.push(productorderRest.create(v));
				}
				else if(v.action==='update'){
					axiosapis.push(productorderRest.update(v.id, v));
				}
				else if(v.action==='delete'){
					axiosapis.push(productorderRest.delete(v.id));
				}
			})
			return Promise.all(axiosapis);
		},
	},
};
</script>

<style lang="scss">
</style>



