<template>
	<div>
		<h2 class="">註冊</h2>
		<div class="container">
			<form-drf v-for="(model_info, key) in model_infos"
				:model_info="model_info"
				:field="key"
				:offset-class="'col-md-offset-2'"
				v-model="input[key]"
				:key="key"
			></form-drf>
			<div class="form-group row">
				<label class="col-md-2" for="userterms"><font style="color:red">*</font><span>使用者條款隱私權政策</span></label>
				<div class="col-md-9">
					<textarea id="userterms" class="form-control" readonly style="width:100%;height: 300px; resize: none;">

					</textarea>
					<label>
						<input type="checkbox" v-model="license">
						<span>我同意此條款</span>
					</label>
				</div>
			</div>
			<div class="form-group row paddTopBtmSm">
				<label class="col-md-2"></label>
				<div class="col-md-7">
					<button class="btn btn-primary" @click="check"><font-awesome-icon icon="check" class="textAside" />註冊</button>
					<button class="btn btn-secondary" @click="clear"><font-awesome-icon icon="times" class="textAside" />清除</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import * as _ from "lodash"
import { token } from '@/utils/token';
import { userRest, userAction, } from '@/api/user';
import formDrf from './form.vue';

export default {
	components: {
		formDrf,
	},
	data(){
		return {
			usertermsContent: '',
			license: true,
			input : {
				username: '',
				password: '',
				confirm_password: '',
				first_name: '',
				last_name: '',
				phone: '',
				birthday: '',
			},
			model_infos : {
				username: {
					'label': '使用者名稱',
					'type': 'email',
					required: true,
					'remark': '請使用電子郵件註冊',
				},
				password: {
					'label': '密碼',
					'type': 'password',
					required: true,
					'remark': '',
				},
				confirm_password: {
					'label': '確認密碼',
					'type': 'password',
					required: true,
					'remark': '',
				},
				first_name: {
					'label': '姓氏',
					'type': 'text',
					'remark': '請填寫中文姓氏',
				},
				last_name: {
					'label': '名字',
					'type': 'text',
					'remark': '請填寫中文名字',
				},
				phone: {
					'label': '聯絡電話',
					'type': 'text',
					'remark': '請填寫手機',
				},
				birthday: {
					'label': '生日',
					'type': 'text',
					'remark': '請填寫生日，範例日期格式：1989-02-19',
				},
			},
		};
	},
	computed: {
	},
	mounted(){
		document.title = '註冊'
	},
	methods: {
		check(){
			let err=[];
			let username = this.input.username;
			if(username.length<5){
				err.push('使用者名稱需5個字元以上');
			}
			else if(username.length>150){
				err.push('使用者名稱需150個字元以下');
			}
			else{
				let r=/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
				if (!r.test(username)){
					err.push('使用者名稱非正確電子郵件格式');
				}
			}
			let password = this.input.password;
			if(password.length<8){
				err.push('密碼需8個字元以上');
			}
			else if(password.length>150){
				err.push('密碼需150個字元以下');
			}
			else{
				let r1=/^[a-zA-Z0-9_]+$/;
				if (!r1.test(password)){
					err.push('密碼需使用大、小寫英文字母、數字與底線');
				}
				let passwordcf = this.input.confirm_password;
				if (password!==passwordcf){
					err.push('密碼與確認密碼不相符');
				}
			}
			//firstname
			let firstname = this.input.first_name;
			if(firstname===''){
				err.push('姓氏不得為空');
			}
			//lastname
			let lastname = this.input.last_name;
			if(lastname===''){
				err.push('名字不得為空');
			}
			//email
			/*let email = this.input.email;
			if(email===''){
				err.push('電子信箱不得為空');
			}
			else{
				let r1=/^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/;
				if (!r1.test(email)){
					err.push('電子信箱格式錯誤');
				}
			}*/
			//phone
			let phone = this.input.phone;
			if(phone===''){
				err.push('聯絡電話不得為空');
			}
			else{
				let r1=/^[0-9]{10}$/;
				if (!r1.test(phone)){
					err.push('手機號碼格式錯誤');
				}
			}
			//birthday
			let birthday = this.input.birthday;
			if(birthday===''){
				err.push('生日不得為空');
			}
			else{
				let r1=/^([0-9]{4})[-]{1}([0-9]{2})[-]{1}([0-9]{2})$/;
				if (!r1.test(birthday)){
					err.push('生日格式錯誤：a.使用年月日的順序。 b.年需使用西元年為4位數字，月與日為2位數字，不足時需補0。 c.年月日中間使用-為分隔，例(1989-02-19)。');
				}
			}
			//show
			if(err.length>0){
				this.$root.$message.open(0, err);
			}
			else{
				// register
				this.sign_up();
			}
		},
		clear(){
			_.each(this.input, (v,k) => {
				this.input[k] = '';
			})
		},
		async sign_up(){
			let res;
			try {
				res = await userRest.create(this.input);
				this.$root.$message.open(0, '成功註冊帳號');
			}
			catch (error) {
				if(error.response.status){
					error = error.response;
					this.$root.$message.open(error.status, error.data);
				} else {
					console.log(error);
					this.$root.$message.open(-1, error);
				}
			}
		},
	},
}
</script>
<style scoped lang="scss">
	@import '../style/base.scss';
</style>



