<template>
	<div>
		<bs-modal id="message" ref="message">
			<template slot="header">
				<h4 class="modal-title">系統訊息</h4>
				<button class="close" @click="close" aria-label="Close"><span>&times;</span></button>
			</template>
			<template slot="body">
				<div v-if="type=='html'">
					<div class="msgAndStatusCtnr">
						<div class="msgCtnr">
							<div v-html="data"></div>
						</div>
						<visual-http-status-code :status-code="status"></visual-http-status-code>
					</div>
				</div>
				<div v-else-if="type=='array'">
					<div class="msgAndStatusCtnr">
						<div class="msgCtnr">
							<div v-for="(item, index) in data">
								{{ index+1 }}. {{ item }}
							</div>
						</div>
						<visual-http-status-code :status-code="status"></visual-http-status-code>
					</div>
				</div>
				<div v-else-if="type=='object'">
					<div class="msgAndStatusCtnr">
						<div class="msgCtnr">
							<div v-for="(value, key, index) in data">
								{{ index+1 }}. {{ key }} : {{ value }}
							</div>
						</div>
						<visual-http-status-code :status-code="status"></visual-http-status-code>
					</div>
				</div>
				<div v-else>
					<div class="preWrap">{{ data }}</div>
					<hr>
					<p>Status code: {{ status }}</p>
				</div>
			</template>
			<template slot="footer">
				<button class="btn btn-primary" @click="close">確定</button>
			</template>
		</bs-modal>
	</div>
</template>

<script>
import bsModal from './modal.vue';
import { isString, isArray, isObject } from '@/utils/type';
import visualHttpStatusCode from './visual_http-status-code';

export default {
	props: {
	},
	name: 'message',
	components: {
			bsModal,
			visualHttpStatusCode,
	},
	data() {
		return {
			status: 0, // 200 success 400 api error 500 server error
			data: '',
			type: null,
		};
	},
	created() {
		this.$root.$message = this;
	},
	methods: {
		open(status, data, type){
			this.status = status;
			if(!type){
				if(isString(data)) {
					this.data = data;
					this.type = 'string';
				}
				else if(isArray(data)) {
					this.data = data;
					this.type = 'array';
				}
				else if(isObject(data)) {
					this.data = data;
					this.type = 'object';
				}
				else {
					this.data = JSON.stringify(data);
					this.type = 'string';
				}
			}
			this.$refs['message'].open('');
			return new Promise((resolve,reject) => {
				this.$on('close', () => {
					console.log('Close!');
					resolve();
				});
			} );
		},
		close(){
			this.$refs['message'].close();
			this.$emit('close');
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';

	/deep/ .msgAndStatusCtnr {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	/deep/ .msgAndStatusCtnr > * {
		flex-grow: 1;
	}
	/deep/ .msgAndStatusCtnr > .httpStatusCodeCtnr {
	}
	.preWrap {
		overflow-wrap: break-word;
	}
</style>



