<template>
	<div id="deliverys_manager">
		<div v-if="mode==='list'">
			<div class="bareBtnCtnr">
				<button
					@click="mode='create'"
					class="btn btn-primary"
				><font-awesome-icon icon="plus" class="textAside" />新增</button>
			</div>
			<template v-for="(item, index) in deliverys">
				<h4>配送{{ index+1 }}</h4>
				<delivery-manager
					:pk="item"
					:key="index"
					@create="create_message"
					@update="update_message"
					@delete="delete_message"
					@error="error_message"
				></delivery-manager>
			</template>
		</div>
		<div v-if="mode==='create'">
			<div class="bareBtnCtnr">
				<button
					@click="mode='list'"
					class="btn btn-primary"
				><font-awesome-icon icon="stream" class="textAside" />回列表</button>
			</div>
			<delivery-manager
				:pk="0"
				:default_order="pk"
				ref="dmc"
				@create="create_message"
				@update="update_message"
				@delete="delete_message"
					@error="error_message"
			></delivery-manager>
		</div>
	</div>
</template>

<script>
import { deliveryRest, order_rest as orderRest } from '@/api/sale';
import deliveryManager from './delivery_manager.vue';

export default {
	props: {
		pk: {
			type: Number,
			default: 0,
		},
	},
	name: 'deliverys_manager',
	components: {
		deliveryManager,
	},
	data() {
		return {
			id: -Math.floor(Math.random() * 100000000).toString(),
			mode: 'list',
			order: {},
			deliverys: [],
		};
	},
	computed: {
	},
	async mounted() {
		await this.load();
	},
	methods: {
		async load(){
			this.mode = 'list';
			this.order = {};
			this.deliverys = [];
			let res;
			try {
				res = await orderRest.read(this.pk);
				this.order = res.data;
				this.deliverys = this.order.delivery_set;
			}
			catch (error) {
				console.log(error);
			}
		},
		create_message(event){
			this.load();
			this.$root.$message.open(0, '成功建立配送' +event.id);
		},
		update_message(event){
			this.load();
			this.$root.$message.open(0, '成功儲存配送' +event.id);
		},
		delete_message(event){
			this.load();
			this.$root.$message.open(0, '成功刪除配送' +event.id);
		},
		error_message(error){
			if(error.status){
				this.$root.$message.open(error.status, error.data);
			} else {
				console.log(error);
				this.$root.$message.open(-1, error);
			}
		},
		deliverys_read_promise(){
			let axiosapis = [];
			this.order.delivery_set.forEach((v, i)=>  {
				axiosapis.push(deliveryRest.read(v));
			})
			return Promise.all(axiosapis);
		},
	},
};
</script>

<style scoped lang="scss">
</style>



