export default {
	debug: true,
	state: {
	},
	setAction (key, value) {
		if (this.debug) console.log('setAction triggered with', key, value)
		this.state[key] = value
	},
	clearAction (key) {
		if (this.debug) console.log('clearAction triggered', key)
		this.state[key] = ''
	}
}


