<template>
	<div>
		<bs-modal id="filter_condition" ref="filter_condition">
			<template slot="header">
				<h4 class="modal-title">查詢條件資料</h4>
				<button class="close" @click="cancel" aria-label="Close"><span>&times;</span></button>
			</template>
			<template slot="body">
				<div v-for="(item, index) in filters" class="ctnr_selectOptnAndRmvBtn">
					<div class="bareBtnCtnr rmvBtn">
						<button
							@click="remove(index)"
							class="btn btn-primary"
							aria-label="移除"
						><font-awesome-icon icon="times" class="textAside" /></button>
					</div>
					<div class="input-group selectOptn">
						<div class="input-group-prepend">
							<label class="input-group-text" for="inputGroupSelect01">選項</label>
						</div>
						<select
							v-model="item.key"
							class="custom-select"
						>
							<option
								v-for="(v, i) in filter_field"
								:value="v.key"
							>{{ v.label }}</option>
						</select>
					</div>
					<template
						v-for="(v, i) in filter_field"
					>
						<div v-if="item.key==v.key" class="selectOptnResult">
							<div v-if="v.type==='boolean'">
								<input type="radio" :id="'true' +index" :value="true" v-model="item.value">
								<label :for="'true' +index">是</label>
								<input type="radio" :id="'false' +index" :value="false" v-model="item.value">
								<label :for="'false' +index">否</label>
							</div>
							<div v-else-if="v.type==='date'">
								<period v-model="item.value"></period>
							</div>
							<div v-else-if="v.type==='select'" class="input-group">
								<select
									v-model="item.value"
									class="custom-select"
								>
									<option
										v-for="(v, i) in v.choices"
										:value="v.value"
									>{{ v.display_name }}</option>
								</select>
							</div>
							<div v-else>
								<input v-model="item.value" type="text"/>
							</div>
						</div>
					</template>
				</div>
			</template>
			<template slot="footer">
				<div class="bareBtnCtnr">
					<button
						@click="add"
						class="btn btn-primary"
						aria-label="增加"
					><font-awesome-icon icon="plus" class="textAside" /></button>
					<button
						@click="ok"
						class="btn btn-primary"
					><font-awesome-icon icon="search" class="textAside" />查詢</button>
				</div>
			</template>
		</bs-modal>
	</div>
</template>

<script>
import bsModal from './modal.vue';
import period from './period.vue';

export default {
	props: ['filter_field',],
	components: {
		bsModal,
		period,
	},
	data() {
		return {
			filters: [{key: '', value: ''}],
		};
	},
	methods: {
		add(){
			if(this.filters.length < this.filter_field.length){
				this.filters.push({key: '', value: ''});
			}
		},
		remove(index){
			this.filters.splice(index,1);
		},
		open(){
			this.$refs['filter_condition'].open('');
			return new Promise((resolve, reject) => {
				this.$on('cancel', () => {
					reject();
				});
				this.$on('ok', () => {
					resolve();
				});
			} );
		},
		cancel(){
			this.$refs['filter_condition'].close();
			this.$emit('cancel');
		},
		ok(){
			let filters = {};
			this.filters.forEach(v => {
				filters[v.key] = v.value;
			});
			this.$refs['filter_condition'].close();
			this.$emit('ok', filters);
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';

	.ctnr_selectOptnAndRmvBtn {
		display: grid;
		grid-template-columns: 1fr max-content;
		justify-content: center;
		align-content: center;
		align-items: center;
		grid-column-gap: $spaceS4;
		grid-row-gap: $spaceS3;
		padding-top: $spaceS3;
		padding-bottom: $spaceS3;
	}
	.ctnr_selectOptnAndRmvBtn .selectOptn {
		grid-column: 1/2;
		grid-row: 1/2;
	}
	.ctnr_selectOptnAndRmvBtn .rmvBtn {
		grid-column: 2/3;
		grid-row: 1/2;
	}
	.ctnr_selectOptnAndRmvBtn .selectOptnResult {
		grid-column: 1/3;
		grid-row: 2/3;
		padding-left: $spaceS3;
		padding-right: $spaceS3;
	}
</style>



