<template>
	<div>
		<h2>銷貨系統</h2>
		<div>登入有效時間：{{ token.get_exp_date('refresh') }}</div>
		<div>使用者編號：{{ token.get_user_id('refresh') }}</div>
		<div class="bareBtnCtnr">
			<button @click="open_token" class="btn btn-primary"><font-awesome-icon icon="user-secret" class="textAside" />Token</button>
		</div>
	</div>
</template>

<script>
import { token } from '@/utils/token';

export default {
	components: {
	},
	data() {
		return {
			token: token,
		};
	},
	methods: {
		open_token(){
			this.$root.$token.open();
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';
</style>



