<template>
	<div id="login">
		<div class="form-group text-center">
			<h2>登錄</h2>
		</div>
		<div class="container">
			<form-drf
				:model_info="model_info.username"
				:field="'username'"
				:offset-class="'col-md-offset-3'"
				v-model="username"
				@keyup.enter.native="login()"
			></form-drf>
			<form-drf
				:model_info="model_info.password"
				:field="'password'"
				:offset-class="'col-md-offset-3'"
				v-model="password"
				@keyup.enter.native="login()"
			></form-drf>

			<div class="form-group">
				<label class="col-md-2"></label>
				<div class="col-md-5">
					<button class="btn btn-primary" @click="login()"><font-awesome-icon icon="user-check" class="textAside" />登錄</button>
					<button class="btn btn-primary" @click="$router.push({name: 'reset_password'})"><font-awesome-icon icon="question" class="textAside" />忘記密碼</button>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import { token } from '@/utils/token';
import { userRest, userAction, } from '@/api/user';
import formDrf from './form.vue';

export default {
	components: {
		formDrf,
	},
	data() {
		return {
			username: '',
			password: '',
			model_info: {
				username: {
					'label': '帳號',
					type: 'text',
				},
				password: {
					'label': '密碼',
					'type': 'password',
				},
			},
		};
	},
	mounted() {

	},
	methods: {
		async login(){
			let res;
			try {
				res = await token.obtain(this.username, this.password);
				this.$root.$message.open(0, '成功登錄系統');
				this.$router.push({name: 'root'})
			}
			catch (error) {
				console.log(error);
				this.$root.$message.open(0, '失敗登錄系統');
			}
		},
	},
}
</script>
<style scoped lang="scss">
	@import '../style/base.scss';
</style>



