<template>
	<div>
		<div class="bareBtnCtnr">
			<button
				@click="add"
				class="btn btn-primary"
			><font-awesome-icon icon="plus" class="textAside" />新增</button>
		</div>
		<table-div
			:header="proposalorder_header"
			:datas="proposalorder_datas"
		>
			<template slot="id" slot-scope="props">
				<div v-if="proposalorders[props.item].mode==='read'">
					{{ proposalorders[props.item].proposal_description }}
				</div>
				<div v-if="proposalorders[props.item].mode==='write'">
					<input v-model="proposalorders[props.item].proposal_description"/>
				</div>
			</template>
			<template slot="name" slot-scope="props">
				<div v-if="proposalorders[props.item].mode==='read'">
					<div v-for="item in proposal_description_list">
						<div v-if="item.id==proposalorders[props.item].proposal_description">
							{{ item.name }}
						</div>
					</div>
				</div>
				<div v-if="proposalorders[props.item].mode==='write'" class="input-group">
					<select v-model="proposalorders[props.item].proposal_description" class="custom-select">
						<option
							v-for="item in proposal_description_list"
							:value="item.id"
						>{{ item.name }}</option>
					</select>
				</div>
			</template>
			<template slot="count" slot-scope="props">
				<div v-if="proposalorders[props.item].mode==='write' && !(proposalorders[props.item].proposal_description==1)">
					<input v-model="proposalorders[props.item].count"/>
				</div>
				<div v-else>
					<div v-if="proposalorders[props.item].proposal_description==1">
						1
					</div>
					<div v-else>
						{{ proposalorders[props.item].count }}
					</div>
				</div>
			</template>
			<template slot="amount" slot-scope="props">
				<div v-if="proposalorders[props.item].mode==='write' && proposalorders[props.item].proposal_description==1">
					<input v-model="proposalorders[props.item].amount"/>
				</div>
				<div v-else>
					<div v-if="proposalorders[props.item].proposal_description==1">
						{{ proposalorders[props.item].amount }}
					</div>
					<div v-else>
						{{ get_proposal_price(proposalorders[props.item].proposal_description) * proposalorders[props.item].count }}
					</div>
				</div>
			</template>
				<template slot="action" slot-scope="props">
					<button
						v-if="proposalorders[props.item].mode==='read'"
						@click="proposalorders[props.item].mode='write'"
						class="btn btn-primary"
					><font-awesome-icon icon="pen" class="textAside" />編輯</button>
					<button
						v-if="proposalorders[props.item].mode==='write'"
						@click="proposalorders[props.item].mode='read'"
						class="btn btn-primary"
					><font-awesome-icon icon="check" class="textAside" />完成</button>
					<button
						@click="remove(props.item)"
						class="btn btn-secondary"
					><font-awesome-icon icon="times" class="textAside" />刪除</button>
				</template>
		</table-div>
		<div class="paddTopBtmSm ctnr_priceAmount">
			<div>付款金額：{{ order.payment_price }}</div>
			<div>方案金額：{{ total_amount }}</div>
			<button
				v-if="pk"
				@click="operate('update')"
				class="btn btn-primary"
			><font-awesome-icon icon="sync-alt" class="textAside" />更新</button>
		</div>
	</div>
</template>

<script>
import {clone} from 'lodash';
import bsModal from './modal.vue';
import tableDiv from './table-div.vue';
import { order_rest as orderRest, proposal_description_rest as proposal_descriptionRest, proposalorderRest } from '@/api/sale';

export default {
	props: {
		pk: {
			type: Number,
			default: 0,
		},
	},
	name: 'order_proposalorders_manager',
	components: {
			bsModal,
		tableDiv,
	},
	data() {
		return {
			proposal_description_list: [], // API 商品列表
			order: {
				id: 0,
				proposalorder_set: [],
			}, // API 取得 order
			ids: [],
			proposalorders: [],
			proposalorder_header: {
				id: '代號',
				name: '名稱',
				count: '數量',
				amount: '金額',
				action: '動作',
			},
		};
	},
	computed: {
		total_amount(){
			let price=0;
			this.proposalorders.forEach((v, i)=>{
				if(v.proposal_description==1){
					price = price +Number(this.proposalorders[i].amount);
				}
				else {
					price = price +Number(this.proposalorders[i].count) * this.get_proposal_price(this.proposalorders[i].proposal_description)
				}


			})
			return price;
		},
		proposalorder_datas(){
			let temp = []
			this.proposalorders.forEach((v, i)=>  {
				temp.push({
					id: i,
					name: i,
					count: i,
					amount: i,
					action: i,
				})
			})
			return temp;
		},
		proposalordersUpdate(){
			let temp = []
			let old_count = this.ids.length;
			let new_count = this.proposalorders.length
			let count = Math.max(new_count, old_count);
			for(let i=0; i<count; i++){
				if(new_count>old_count){
					if(i<old_count){
						temp.push({
							id: this.ids[i].id,
							order: this.pk,
							proposal_description: this.proposalorders[i].proposal_description,
							count: this.proposalorders[i].count,
							amount: this.proposalorders[i].amount,
							action: 'update',
						})
					}
					else 					if(i>=old_count){
						temp.push({
							id: null,
							order: this.pk,
							proposal_description: this.proposalorders[i].proposal_description,
							count: this.proposalorders[i].count,
							amount: this.proposalorders[i].amount,
							action: 'create',
						})
					}
				}
				else {
					if(i<new_count){
						temp.push({
							id: this.ids[i].id,
							order: this.pk,
							proposal_description: this.proposalorders[i].proposal_description,
							count: this.proposalorders[i].count,
							amount: this.proposalorders[i].amount,
							action: 'update',
						})
					}
					else 					if(i>=new_count){
						temp.push({
							id: this.ids[i].id,
							action: 'delete',
						})
					}
				}
			}
			return temp
		},
	},
	watch: {
	},
	async mounted() {
		await this.load();
	},
	methods: {
		async load(){
			let res;
			try {
				res = await Promise.all([
					proposal_descriptionRest.list(),
				])
				this.proposal_description_list = res[0].data;

				if(this.pk){
					res = await orderRest.read(this.pk);
					this.order = res.data;
					await this.order2data();
				}
			}
			catch (error) {
				console.log(error);
			}
		},

		async order2data(){
			let res;
			res = await this.proposalorders_read_promise();
			this.proposalorders = [];
			this.ids = [];
			res.forEach((v, i)=>  {
				this.proposalorders.push({
					id: v.data.id,
					proposal_description: v.data.proposal_description,
					count: v.data.count,
					amount: v.data.amount,
					mode: 'read',
				});
			})
			res.forEach((v, i)=>  {
				this.ids.push({
					id: v.data.id,
				});
			})
		},
		add(){
			this.proposalorders.push({
				id: null,
				proposal_description: 1,
				count: 0,
				amount: 0,
				mode: 'write',
			});
		},
		remove(index){
			this.proposalorders.splice(index,1);
			this.proposalorders.forEach((v, i)=>  {
				v.order = i;
			})
		},
		get_proposal_price(id){
			let price=0;
			this.proposal_description_list.forEach((v, i)=>  {
				if(v.id==id){
					price = v.price;
				}
			})
			return Number(price);
		},
		async operate(action){
			let res;
			if(action=='create'){
				try {
					res = await this.proposalorders_create_promise();
					res = await orderRest.read(this.pk);
					this.order = res.data;
					this.order2data();
				}
				catch (error) {
					console.log(JSON.stringify(error.response));
				}
			}
			if(action=='read'){
				try {
					res = await orderRest.read(this.pk)
					this.order = res.data;
					this.order2data();
				}
				catch (error) {
					console.log(JSON.stringify(error.response));
				}
			}
			else if(action=='update'){
				try {
					res = await this.proposalorders_update_promise();
					res = await orderRest.read(this.pk);
					this.order = res.data;
					this.order2data();
					this.$emit('update', res.data);
					if(!(this.order.payment_price==this.total_amount)){
						this.$root.$message.open(0, '成功更新方案資訊，但付款金額與方案金額不符，請對付款進行調整！');
					}
					else {
						this.$root.$message.open(0, '成功更新方案資訊');
					}
				}
				catch (error) {
					if(error.status){
						this.$root.$message.open(error.status, error.data);
					} else {
						console.log(error);
						this.$root.$message.open(-1, error);
					}
				}
			}

		},
		proposalorders_create_promise(){
			let axiosapis = [];
			this.proposalorders.forEach((v, i)=>  {
				v['id'] = null;
				v['order'] = this.pk;
				axiosapis.push(proposalorderRest.create(v));
			})
			return Promise.all(axiosapis);
		},
		proposalorders_read_promise(){
			let axiosapis = [];
			this.order.proposalorder_set.forEach((v, i)=>  {
				axiosapis.push(proposalorderRest.read(v));
			})
			return Promise.all(axiosapis);
		},
		proposalorders_update_promise(){
			let axiosapis = [];
			this.proposalordersUpdate.forEach((v, i)=>  {
				if(v.action==='create'){
					axiosapis.push(proposalorderRest.create(v));
				}
				else if(v.action==='update'){
					axiosapis.push(proposalorderRest.update(v.id, v));
				}
				else if(v.action==='delete'){
					axiosapis.push(proposalorderRest.delete(v.id));
				}
			})
			return Promise.all(axiosapis);
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';

	.ctnr_priceAmount {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.ctnr_priceAmount > * {
		margin-left: $spaceS3;
		margin-right: $spaceS3;
	}
</style>



