<template>
	<div>
		<bs-modal id="token" ref="token">
			<template slot="header">
				<h4 class="modal-title">token 資訊</h4>
			</template>
			<template slot="body">
				<h5>access</h5>
				<div>到期時間：{{ token.get_exp_date('access') }}</div>
				<div>使用者：{{ token.get_user_id('access') }}</div>
				<div>到期倒數：{{ token.get_exp_countdown('access') }}</div>
				<h5>refresh</h5>
				<div>到期時間：{{ token.get_exp_date('refresh') }}</div>
				<div>使用者：{{ token.get_user_id('refresh') }}</div>
				<div>到期倒數：{{ token.get_exp_countdown('refresh') }}</div>
				<button class="btn btn-primary" @click="refresh">刷新</button>
			</template>
			<template slot="footer">
				<button class="btn btn-primary" @click="close">確定</button>
			</template>
		</bs-modal>
	</div>
</template>

<script>
import bsModal from './modal.vue';
import { token } from '@/utils/token';

export default {
	props: {
	},
	name: 'token',
	components: {
			bsModal,
	},
	data() {
		return {
			token: token,
		};
	},
	created() {
		this.$root.$token = this;
	},
	methods: {
		open(){
			let focus_after_closed='';
			this.$refs['token'].open(focus_after_closed);
		},
		close(){
			this.$refs['token'].close();
		},
		async refresh(){
			let res;
			try {
				res = await this.token.refresh();
				this.$root.$message.open(0, '成功刷新 token', '');
			}
			catch (error) {
				console.log(error);
				this.$root.$message.open(0, '失敗刷新 token', '');
			}
		},
	},
};
</script>

<style lang="scss">
</style>



