<template>
	<div id="retrieve">
		<div class="form-group text-center">
			<h2>重設密密碼</h2>
		</div>

		<div id="reset_password">
			<h4>重設密碼</h4>
			<div class="form-horizontal">
				<form-drf 
					:model_info="model_info.username"
					:field="'username'"
					v-model="username"
					@keyup.enter.native="reset_password()"
				></form-drf>
				<form-drf 
					:model_info="model_info.birthday"
					:field="'birthday'"
					v-model="birthday"
					@keyup.enter.native="reset_password()"
				></form-drf>
				<div class="form-group">
					<div class="col-sm-3 col-sm-offset-2">
						<button class="form-control btn btn-primary" @click="reset_password()">重設</button>
					</div>
				</div>
			</div>
		</div>

	</div>
</template>
<script>
import { userAction, } from '@/api/user';
import formDrf from './form.vue';

export default {
	components: {
		formDrf,
	},
	data(){
		return {
			username: '',
			birthday: '',
			model_info: {
				username: {
					'label': '帳號',
					'type': 'text',
				},
				birthday: {
					'label': '生日',
					'type': 'text',
				},
			},
		};
	},
	mounted(){
		document.title = '重設密碼'
	},
	methods: {
		async reset_password(){
			let res;
			try {
				res = await userAction.reset_password(this.username, this.birthday);
				this.$root.$message.open(0, '成功重設密碼', '');
			}
			catch (error) {
				console.log(error);
				this.$root.$message.open(0, '失敗重設密碼', '');
			}
		},
	},
}
</script>


