<template>
	<div>
		<h2>付款列表</h2>
		<filters
			:filter_field="instance_filter_field"
			@ok="search"
			ref="fc"
		></filters>
		<div class="btn-group paddTopBtmSm the4btn">
			<button class="btn btn-primary" @click="select_all"><font-awesome-icon icon="check" class="textAside" />全選</button>
			<button class="btn btn-primary" @click="select_items=[]"><font-awesome-icon icon="times" class="textAside" />全不選</button>
			<button class="btn btn-primary" @click="select_inv"><font-awesome-icon icon="exchange-alt" class="textAside" />反向選</button>
			<button class="btn btn-primary" @click="$refs['fc'].open();"><font-awesome-icon icon="filter" class="textAside" />查詢條件</button>
		</div>
		<table-div
			:header="instance_header"
			:datas="instance_datas"
		>
			<template slot="id" slot-scope="props">
				<label>
					<input type="checkbox" v-model="select_items" :value="props.item" class="visu_verticalAlign">
					<span class="visu_verticalAlign">{{ props.item }}</span>
				</label>
				<!-- ."序號" field, number and checkbox. -->
			</template>
			<template slot="paid" slot-scope="props">
				<div v-if="props.item===true">是</div>
				<div v-if="props.item===false">否</div>
			</template>
			<template slot="action" slot-scope="props">
				<button
					@click="$router.push({ name: 'manage', params: { id: props.item.orderobj }});"
					class="btn btn-primary"
				>訂單</button>
			</template>
		</table-div>
		<div class="paddTopBtmSm">總金額：{{ total_amount }}</div>
		<div>
			<select
				v-model="action"
				class="custom-select"
			>
				<option value="download_list">下載匯出列表</option>
				<option value="batch_update">批次更新資料</option>
			</select>
			<div class="bareBtnCtnr goBtnCtnr">
				<button
					@click="acton"
					class="btn btn-primary"
				>GO !</button>
			</div>
		</div>
		<batch-update
			:fields="instance_batch_fields"
			@ok="batchupdate"
			ref="bu"
		></batch-update>
	</div>
</template>

<script>
import { Token, token } from '@/utils/token';
import { paymentRest, paymentAction, } from '@/api/sale';
import bsModal from './modal.vue';
import tableDiv from './table-div.vue';
import batchUpdate from './batch_update.vue';
import filters from './filter.vue';

export default {
	components: {
		bsModal,
		tableDiv,
		batchUpdate,
		filters,
	},
	data() {
		return {
			action: 'paid',
			instance_filter_field: [
				{
					key: 'date',
					type: 'date',
					label: '日期',
				},
				{
					key: 'paid',
					type: 'boolean',
					label: '支付',
				},
				{
					key: 'status',
					type: 'select',
					label: '狀態',
					'choices' : [
						{
							'value': '0',
							'display_name': '資料已建立',
						},
						{
							'value': '1',
							'display_name': '資料已確認',
						},
						{
							'value': '2',
							'display_name': '發票或收據已開立',
						},
					],
				},
			],
			instance_batch_fields: [
				{
					key: 'date',
					type: 'date',
					label: '日期',
				},
				{
					key: 'paid',
					type: 'boolean',
					label: '支付',
				},
			],
			instances: [],
			instance_datas: [],
			instance_header: {
				id: '序號',
				date: '日期',
				amount: '金額',
				paid: '支付',
				action: '動作',
			},
			select_items: [],
		};
	},
	computed: {
		total_amount(){
			let price=0;
			this.select_items.forEach((v, i)=>{
				price = price +this.get_instance_amount(v);
			})
			return price;
		},
	},
	async mounted() {
		await this.load();
	},
	methods: {
		get_instance_amount(id){
			let temp = 0;
			this.instances.forEach((v, i)=>  {
				if(v.id==id){
					temp = v.amount;
				}
			})
			return Number(temp);
		},
		async load() {
			let res;
			this.instance_datas = [];
			try {
				res = await paymentRest.list();
				this.instances = res.data;
				this.instance_datas = [];
				this.instances.forEach((v, i)=>  {
					this.instance_datas.push({
						id: v.id,
						date: v.date,
						amount: v.amount,
						paid: v.paid,
						action: v,
					})
				})
			}
			catch (error) {
				console.log(error);
			}
		},
		async acton(){
			let res;
			if(this.action==='paid'){
				try {
					res = await paymentAction.paid(this.select_items);
					await this.load();
					this.$root.$message.open(200, '成功更新付款資訊');
				}
				catch (error) {
					if(error.status){
						this.$root.$message.open(error.status, error.data);
					} else {
						console.log(error);
						this.$root.$message.open(-1, error);
					}
				}
			}
			else if(this.action==='download_list'){
				paymentAction.list(this.select_items);
			}
			else if(this.action==='batch_update'){
				this.$refs['bu'].open();
			}
		},
		async batchupdate(event){
			let res;
			try {
				res = await paymentAction.batchupdate(this.select_items, event);
				this.$root.$message.open(200, '成功批次更新資料');
				await this.load();
			}
			catch (error) {
				if(error.response.status){
					this.$root.$message.open(error.response.status, error.response.data);
				} else {
					console.log(error);
					this.$root.$message.open(-1, error);
				}
			}
		},
		async search(event){
			let res;
			try {
				res = await paymentRest.filter(event);
				this.$root.$message.open(0, '成功查詢資料' +res.data.length, '');
				this.instances = res.data;
				this.instance_datas = [];
				this.instances.forEach((v, i)=>  {
					this.instance_datas.push({
						id: v.id,
						date: v.date,
						amount: v.amount,
						paid: v.paid,
						action: v.id,
					})
				})
			}
			catch (error) {
				console.log(error);
				this.$root.$message.open(1, error.response, '');
			}
		},
		select_all(){
			this.instance_datas.forEach((v, i)=>  {
				this.select_items.push(v.id)
			})
		},
		select_inv(){
			let temp = []
			this.instance_datas.forEach((v, i)=> {
				if(!_.includes(this.select_items, v.id)){
					temp.push(v.id);
				}
			})
			this.select_items = temp;
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';

	.visu_verticalAlign {
		vertical-align: middle;
	}
	.the4btn {
		padding-top: $spaceS5;
		padding-bottom: $spaceS3;
	}
	.goBtnCtnr {
		padding-top: $spaceS5;
		padding-bottom: $spaceS5;
	}
</style>



