<template>
	<div class="modal fade" :id="id" tabindex="-1" role="dialog" aria-hidden="true">
		<div class="modal-dialog" role="document">
			<div class="modal-content">
				<div class="modal-header">
					<slot name="header">
						<h2>default title</h2>
						<button class="close" data-dismiss="modal" aria-label="Close"><span>&times;</span></button>
					</slot>
				</div>
				<div class="modal-body">
					<slot name="body">
						<p>default body</p>
					</slot>
				</div>
				<div class="modal-footer">
					<slot name="footer">
						<button class="btn btn-primary" @click="close">關閉</button>
					</slot>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import $ from 'jquery';
import '@/utils/modal/dialog.js';
import '@/utils/modal/dialog.css';

export default {
	props: {
		id: String,
	},
	methods: {
		/*open: function(focusAfterClosed){
			openDialog(this.id, focusAfterClosed)
		},*/
		open: function(focusAfterClosed){
			$('#' +this.id).modal('show');
		},
		close: function(){
			$('#' +this.id).modal('hide');
		},
	},
};
</script>
<style scoped lang="scss">
	@import '../style/base.scss';
	.modal-header h2,
	.modal-header h3,
	.modal-header h4 {
		margin-top: $spaceS2;
		margin-bottom: $spaceS2;
	}
</style>



