<template>
	<div>
		<h2>出貨列表</h2>
		<filters
			:filter_field="instance_filter_field"
			@ok="search"
			ref="fc"
		></filters>
		<div class="btn-group paddTopBtmSm the4btn">
			<button class="btn btn-primary" @click="select_all"><font-awesome-icon icon="check" class="textAside" />全選</button>
			<button class="btn btn-primary" @click="select_items=[]"><font-awesome-icon icon="times" class="textAside" />全不選</button>
			<button class="btn btn-primary" @click="select_inv"><font-awesome-icon icon="exchange-alt" class="textAside" />反向選</button>
			<button class="btn btn-primary" @click="$refs['fc'].open();"><font-awesome-icon icon="filter" class="textAside" />查詢條件</button>
		</div>
		<table-div
			:header="instance_header"
			:datas="instance_datas"
		>
			<template slot="id" slot-scope="props">
				<label>
					<input type="checkbox" v-model="select_items" :value="props.item" class="visu_verticalAlign">
					<span class="visu_verticalAlign">{{ props.item }}</span>
				</label>
				<!-- ."序號" field, number and checkbox. -->
			</template>
			<template slot="count" slot-scope="props">
				<button
					@click="review_count(props.item)"
					class="btn btn-primary"
				>檢視</button>
			</template>
			<template slot="deliveried" slot-scope="props">
				<div v-if="props.item===true">是</div>
				<div v-if="props.item===false">否</div>
			</template>
			<template slot="action" slot-scope="props">
				<button
					@click="$router.push({ name: 'manage', params: { id: props.item.order }});"
					class="btn btn-primary"
				>訂單</button>
			</template>
		</table-div>
		<div>
			<select
				v-model="action"
				class="custom-select"
			>
				<option value="outdocx">匯出出貨單</option>
				<option value="batch_update">批次更新資料</option>
			</select>
			<div class="bareBtnCtnr goBtnCtnr">
				<button
					@click="acton"
					class="btn btn-primary"
				>GO !</button>
			</div>
		</div>
		<batch-update
			:fields="instance_batch_fields"
			@ok="batchupdate"
			ref="bu"
		></batch-update>
		<bs-modal id="instance_count" ref="instance_count">
			<template slot="header">
				<h4 class="modal-title">商品數量</h4>
			</template>
			<template slot="body">
				<product-deliverys-manager
					:pk="pointer_id"
					mode="read"
					ref="pdm"
				></product-deliverys-manager>
			</template>
		</bs-modal>
	</div>
</template>

<script>
import { Token, token } from '@/utils/token';
import { deliveryRest, deliveryAction, deliveryResource, product_rest as productRest } from '@/api/sale';
import bsModal from './modal.vue';
import tableDiv from './table-div.vue';
import tableSplit from './table_split.vue';
import batchUpdate from './batch_update.vue';
import filters from './filter.vue';
import productDeliverysManager from './product_deliverys_manager.vue';

export default {
	components: {
		bsModal,
		tableDiv,
		tableSplit,
		batchUpdate,
		filters,
		productDeliverysManager,
	},
	data() {
		return {
			action: 'deliveried',
			index: 0,
			pointer_id: 0,
			instance_filter_field: [
				{
					key: 'date',
					type: 'date',
					label: '日期',
				},
				{
					key: 'deliveried',
					type: 'boolean',
					label: '出貨',
				},
				{
					key: 'status',
					type: 'select',
					label: '狀態',
					'choices' : [
						{
							'value': '0',
							'display_name': '資料已建立',
						},
						{
							'value': '1',
							'display_name': '資料已確認',
						},
						{
							'value': '2',
							'display_name': '發票或收據已開立',
						},
					],
				},
			],
			instance_batch_fields: [
				{
					key: 'date',
					type: 'date',
					label: '日期',
				},
				{
					key: 'deliveried',
					type: 'boolean',
					label: '出貨',
				},
			],
			instances: [],
			instance_datas: [],
			instance_header: {
				id: '序號',
				date: '日期',
				count: '數量',
				deliveried: '出貨',
				action: '動作',
			},
			product_count_header:{
				name: '名稱',
				count: '數量',
			},
			product_list: [], // API 商品列表
			select_items: [],
		};
	},
	computed: {
		product_count_datas(){
			let temp = [];
			try {

				return temp;
			}
				catch(error){
				return [];
			}
		},
	},
	async mounted() {
		let res;
		res = await productRest.list();
		this.product_list = res.data;
		await this.load();
	},
	methods: {
		async load(){
			let res;
			try {
				res = await deliveryRest.list();
				this.instances = res.data;
				this.instance_datas = [];
				this.instances.forEach((v, i)=>  {
					this.instance_datas.push({
						id: v.id,
						date: v.date,
						count: i,
						deliveried: v.deliveried,
						action: v,
					})
				})
			}
			catch (error) {
				console.log(error);
			}
		},
		review_count(index){
			this.index = index;
			this.pointer_id = Number(this.instances[index].id);
			this.$refs['pdm'].load();
			this.$refs['instance_count'].open('');
		},
		async acton(){
			let res;
			if(this.action==='deliveried'){
				try {
					res = await deliveryAction.deliveried(this.select_items);
					await this.load();
					this.$root.$message.open(200, '成功更新出貨資訊');
				}
				catch (error) {
					if(error.status){
						this.$root.$message.open(error.status, error.data);
					} else {
						console.log(error);
						this.$root.$message.open(-1, error);
					}
				}
			}
			else if(this.action==='outdocx'){
				await deliveryAction.outdocx(this.select_items);

			}
			else if(this.action==='batch_update'){
				this.$refs['bu'].open();
			}
		},
		async batchupdate(event){
			let res;
			try {
				res = await deliveryAction.batchupdate(this.select_items, event);
				await this.load();
				this.$root.$message.open(200, '成功批次更新資料');
			}
			catch (error) {
				if(error.response.status){
					this.$root.$message.open(error.response.status, error.response.data);
				} else {
					console.log(error);
					this.$root.$message.open(-1, error);
				}
			}
		},
		async search(event){
			let res;
			try {
				res = await deliveryRest.filter(event);
				this.$root.$message.open(0, '成功查詢資料' +res.data.length, '');
				this.instances = res.data;
				this.instance_datas = [];
				this.instances.forEach((v, i)=>  {
					this.instance_datas.push({
						id: v.id,
						date: v.date,
						count: v.id,
						deliveried: v.deliveried,
						action: v.id,
					})
				})
			}
			catch (error) {
				console.log(error);
				this.$root.$message.open(1, error.response, '');
			}
		},
		select_all(){
			this.instance_datas.forEach((v, i)=>  {
				this.select_items.push(v.id)
			})
		},
		select_inv(){
			let temp = []
			this.instance_datas.forEach((v, i)=> {
				if(!_.includes(this.select_items, v.id)){
					temp.push(v.id);
				}
			})
			this.select_items = temp;
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';

	.visu_verticalAlign {
		vertical-align: middle;
	}
	.the4btn {
		padding-top: $spaceS5;
		padding-bottom: $spaceS3;
	}
	.goBtnCtnr {
		padding-top: $spaceS5;
		padding-bottom: $spaceS5;
	}
</style>



