<template>
	<div>
		<bs-modal id="batch_update" ref="batch_update">
			<template slot="header">
				<h4 class="modal-title">批次更新資料</h4>
				<button class="close" @click="cancel" aria-label="Close"><span>&times;</span></button>
			</template>
			<template slot="body">
				<div class="form-group input-group">
					<div class="input-group-prepend">
						<span class="input-group-text">欄位：</span>
					</div>
					<select
						v-model="key"
						class="custom-select"
					>
						<option
							v-for="(v, i) in fields"
							:value="v.key"
						>{{ v.label }}</option>
					</select>
				</div>
				<template
					v-for="(v, i) in fields"
				>
					<div v-if="key==v.key" class="form-group input-group">
						<div class="input-group-prepend">
							<span class="input-group-text">資料：</span>
						</div>
						<div v-if="v.type==='boolean'" class="subFormBodyFix">
							<div class="form-check form-check-inline">
								<input
									type="radio"
									id="true"
									:value="true"
									v-model="value"
									class="form-check-input"
								>
								<label for="true" class="form-check-label">是</label>
							</div>
							<div class="form-check form-check-inline">
								<input
									type="radio"
									id="false"
									:value="false"
									v-model="value"
									class="form-check-input"
								>
								<label for="false" class="form-check-label">否</label>
							</div>
						</div>
						<div v-else-if="v.type==='select'">
							<select
								v-model="value"
							>
								<option
									v-for="(v, i) in v.choices"
									:value="v.value"
								>{{ v.display_name }}</option>
							</select>
						</div>
						<div v-else>
							<input v-model="value" type="text" class="form-control" />
						</div>
					</div>
				</template>
			</template>
			<template slot="footer">
				<button class="btn btn-primary" @click="cancel">取消</button>
				<button class="btn btn-primary" @click="ok">確定</button>
			</template>
		</bs-modal>
	</div>
</template>

<script>
import bsModal from './modal.vue';

export default {
	props: {
		fields: {
			type: Array,
			default: {},
		},
	},
	name: 'batch_update',
	components: {
			bsModal,
	},
	data() {
		return {
				key: '',
				value: '',
		};
	},
	methods: {
		open(){
			this.$refs['batch_update'].open('');
			return new Promise((resolve, reject) => {
				this.$on('cancel', () => {
					reject();
				});
				this.$on('ok', () => {
					resolve();
				});
			} );
		},
		cancel(){
			this.$refs['batch_update'].close();
			this.$emit('cancel');
		},
		ok(){
			let temp = {[this.key]: this.value,};
			this.$refs['batch_update'].close();
			this.$emit('ok', temp)
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';

	.subFormBodyFix {
		// .Purpose: For some task, using Bootstrap will having some issue and tricky,
		// so I use my own "dirty fix", it will be much faster.
		// .About: Making inline elements vertical align.
		display: flex;
		flex-wrap: nowrap;
		//justify-contents: center;
		// .This will not work since this flex container only have limited wide itself.
		align-content: center;
		align-items: center;
		margin-left: $spaceS4;
	}
</style>



