<template>
	<div id="app">
		<router-view/>
	</div>
</template>

<script>
export default {
	name: 'App'
};
</script>

<style lang="scss">
//@import url('../node_modules/font-awesome/css/font-awesome.min.css');
// .Useless, since we now using "@fortawesome" now (font-awesome v5, not v4).
@import './style/base.scss';

	#app {
		// .Used for sticky footer. see comment id 190513m140524.
		// ------------------------------------------------------
		width: 100%;
		min-height: 100%;
		display: grid;
		grid-template-rows: minmax(1fr, max-content);
		// .Direction: Vertical.
		// .Purpose: #app have 2 restriction - 1. Height will never less than min-height.
		// 2. If content inside (grid items) grow, #app have to be responsive to this.
		align-items: stretch;
	}
</style>



