<template>
	<div>
		<div class="container">
			<formdrf
				:model_info="model_info.create_date"
				:field="'create_date'"
				:offset-class="'col-sm-offset-3'"
				v-model="input.create_date"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.name"
				:field="'name'"
				:offset-class="'col-sm-offset-3'"
				v-model="input.name"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.address"
				:field="'address'"
				:offset-class="'col-sm-offset-3'"
				v-model="input.address"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.zip"
				:field="'zip'"
				:offset-class="'col-sm-offset-3'"
				v-model="input.zip"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.company"
				:field="'company'"
				:offset-class="'col-sm-offset-3'"
				v-model="input.company"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.phone"
				:field="'phone'"
				:offset-class="'col-sm-offset-3'"
				v-model="input.phone"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.sex"
				:field="'sex'"
				:offset-class="'col-sm-offset-3'"
				v-model="input.sex"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.source"
				:field="'source'"
				:offset-class="'col-sm-offset-3'"
				v-model="input.source"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.installment"
				:field="'installment'"
				:offset-class="'col-sm-offset-3'"
				v-model="input.installment"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				v-if="input.receipt===true"
				:model_info="model_info.receipt_donation"
				:field="'receipt_donation'"
				:offset-class="'col-sm-offset-3'"
				v-model="input.receipt_donation"
				@keyup.enter.native=""
			></formdrf>
			<template
				v-if="input.receipt_donation===false && input.receipt===true"
			>
				<formdrf
					:model_info="model_info.receipt_number"
					:field="'receipt_number'"
					:offset-class="'col-sm-offset-3'"
					v-model="input.receipt_number"
					@keyup.enter.native=""
				></formdrf>
				<formdrf
					:model_info="model_info.receipt_title"
					:field="'receipt_title'"
					:offset-class="'col-sm-offset-3'"
					v-model="input.receipt_title"
					@keyup.enter.native=""
				></formdrf>
			</template>
			<formdrf
				v-if="input.receipt===false"
				:model_info="model_info.id_number"
				:field="'id_number'"
				:offset-class="'col-sm-offset-3'"
				v-model="input.id_number"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.payment_category"
				:field="'payment_category'"
				:offset-class="'col-sm-offset-3'"
				v-model="input.payment_category"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.payment_info"
				:field="'payment_info'"
				:offset-class="'col-sm-offset-3'"
				v-model="input.payment_info"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.remark"
				:field="'remark'"
				:offset-class="'col-sm-offset-3'"
				v-model="input.remark"
				@keyup.enter.native=""
			></formdrf>
		</div>
		<!--<button
			@click="create()"
		>送出</button>-->
	</div>
</template>

<script>
import { Date2String, } from '@/utils/date';
import formdrf from './form.vue';
import tableDiv from './table-div.vue';
import { order_rest, product_rest as productRest, proposal_description_rest as proposalDescriptionRest, sourceDMRest, } from '@/api/sale';

export default {
	components: {
		formdrf: formdrf,
		tableDiv,
	},
	data() {
		return {
			pk: null,
			proposal_description_list: [], // 方案 API 案描述列表
			product_list: [], // API 商品列表
			productorder_set: [],
			proposal_count: [],
			proposal_count_header: {
				id: '代號',
				name: '名稱',
				count: '數量',
				amount: '金額',
			},
			input: {
				name: '',
				address: '',
				zip: 0,
				company: '',
				phone: '',
				sex: 'man',
				source: '',
				create_date: Date2String(new Date()),
				installment: 'false',
				receipt: true,
				receipt_donation: true,
				receipt_number: '',
				receipt_title: '',
				id_number: '',
				payment_category: '信用卡',
				payment_info: '',
				remark: '',
				delivery_set: [],
				proposal_set: [],
			},
			model_info: {
				create_date: {
					'label': '訂單建立日期',
					'type': 'text',
				},
				name: {
					'label': '姓名',
					'type': 'text',
					required: true,
				},
				address: {
					'label': '地址',
					'type': 'text',
					required: true,
				},
				zip: {
					'label': '郵地區號',
					'type': 'text',
					required: true,
				},
				company: {
					'label': '公司/單位',
					'type': 'text',
				},
				phone: {
					'label': '聯絡電話',
					'type': 'text',
					required: true,
				},
				sex: {
					'label': '性別',
					'type': 'select',
						'choices' : [
							{
								'value': 'man',
								'display_name': '男',
							},
							{
								'value': 'woman',
								'display_name': '女',
							},
						],
				},
				source: {
					'label': '來源(DM)',
					'type': 'select',
					'choices' : [
					],
					required: true,
				},
				create_date: {
					'label': '訂單建立日期',
					'type': 'text',
					required: true,
				},
				installment: {
					'label': '分期付款',
					'type': 'select',
						'choices' : [
							{
								'value': 'false',
								'display_name': '否',
							},
							{
								'value': 'true',
								'display_name': '是',
							},
						],
				},
				receipt: {
					'label': '類別',
					'type': 'select',
						'choices' : [
							{
								'value': '',
								'display_name': '---------',
							},
							{
								'value': false,
								'display_name': '捐贈',
							},
							{
								'value': true,
								'display_name': '購買',
							},
						],
				},
				receipt_donation: {
					'label': '發票捐贈',
					'type': 'checkbox',
				},
				receipt_number: {
					'label': '發票統編',
					'type': 'text',
				},
				receipt_title: {
					'label': '發票抬頭',
					'type': 'text',
				},
				id_number: {
					'label': '身份證字號或統編',
					'type': 'text',
				},
				payment_category: {
					'label': '付款方式',
					'type': 'select',
					'choices' : [
						{
							'value': '信用卡',
							'display_name': '信用卡',
						},
						{
							'value': '轉帳',
							'display_name': '轉帳',
						},
						{
							'value': '郵政劃撥',
							'display_name': '郵政劃撥',
						},
						],
				},
				payment_info: {
					'label': '付款資訊',
					'type': 'text',
				},
				remark: {
					'label': '備註',
					'type': 'text',
				},
			},
		};
	},
	computed: {
		amount(){
			let price=0;
			this.proposal_count.forEach((v, i)=>{
				if(v.id>1){
					price = price +Number(this.proposal_count[i].count*this.get_proposal_price(this.proposal_count[i].id));
				}
				else {
					price = price +Number(this.proposal_count[i].amount);
				}
			})
			return price;
		},
		proposal_count_datas(){
			let temp = []
			this.proposal_count.forEach((v, i)=>  {
				temp.push({
					id: i,
					name: i,
					count: i,
					amount: i,
				})
			})
			return temp;
		},
	},
	async mounted() {
		let res;
		try {
			res = await Promise.all([
				proposalDescriptionRest.list(),
				productRest.list(),
				sourceDMRest.list(),
			])
			this.proposal_description_list = res[0].data;
			this.product_list = res[1].data;
			this.model_info.source.choices = [{
				value: '',
				display_name: '請選擇',
			}];
			res[2].data.forEach((v, i)=>{
				this.model_info.source.choices.push({
					value: v.name,
					display_name: v.name,
				})
			})

		}
		catch (error) {
			console.log(error);
		}
	},
	methods: {
		add(){
			this.proposal_count.push({
				id: '',
				name: '',
				count: 0,
				amount: 0,
			});
		},
		remove(index){
			this.proposal_count.splice(index,1);
		},
		get_proposal_price(id){
			let price=0;
			this.proposal_description_list.forEach((v, i)=>  {
				if(v.id==id){
					price = v.price;
				}
			})
			return price;
		},

		async create(){
			let res;
			try {
				res = await order_rest['create'](this.input);
				this.pk = res.data.id;
				return res;
			}
			catch (error) {
				throw(error.response);
			}
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';
</style>



