import { Token, token } from '@/utils/token';

export const set_token = async config => {
	// Do something before request is sent
	if(token.check_active('access')){
		console.log('active');
		config.headers['Authorization'] = 'Bearer ' +token.token.access;
	}
	else if(token.check_active('refresh')){
		let res;
		res = await token.refresh();
		console.log('refresh');
		config.headers['Authorization'] = 'Bearer ' +token.token.access;
	}
	else {
		console.log('expire');
	}
	return config;
}



