import axios from 'axios';
import { create_rest, create_resource, host, file_resolve } from './base.js';
import { set_token, } from './token.js';

const order = axios.create({
	baseURL: host +'sale/api/orders/'
});

order.interceptors.request.use(set_token, error => {
	// Do something with request error
	return Promise.reject(error);
});

const delivery = axios.create({
	baseURL: host +'sale/api/deliverys/'
});
delivery.interceptors.request.use(set_token, error => {
	// Do something with request error
	return Promise.reject(error);
});

const productdelivery = axios.create({
	baseURL: host +'sale/api/productdeliverys/'
});
productdelivery.interceptors.request.use(set_token, error => {
	// Do something with request error
	return Promise.reject(error);
});

const productorder = axios.create({
	baseURL: host +'sale/api/productorders/'
});
productorder.interceptors.request.use(set_token, error => {
	// Do something with request error
	return Promise.reject(error);
});

const proposalorder = axios.create({
	baseURL: host +'sale/api/proposalorders/'
});
proposalorder.interceptors.request.use(set_token, error => {
	// Do something with request error
	return Promise.reject(error);
});

const proposal_description = axios.create({
	baseURL: host +'sale/api/proposaldescriptions/'
});

const sourceDM = axios.create({
	baseURL: host +'sale/api/sourcedms/'
});

const product = axios.create({
	baseURL: host +'sale/api/products/'
});

const payment = axios.create({
	baseURL: host +'sale/api/payments/'
});
payment.interceptors.request.use(set_token, error => {
	// Do something with request error
	return Promise.reject(error);
});

export const order_rest = create_rest(order);
export const orderAction = {
	default_payment: (pk, installment) => order.post(`/${pk}/action/default_payment/`, {installment,}),
	batchupdate: (items, batchupdate) => order.post('action/batchupdate/', {items, batchupdate,}),
}
export const deliveryRest = create_rest(delivery);
export const deliveryResource = create_resource(delivery);
export const deliveryAction = {
	list: (items) => {
		return delivery.post(
			'action/list/',
			{items,},
			{responseType: 'blob',}
		)
			.then(file_resolve('list.csv'))
	},
	outdocx: (items) => {
		return delivery.post(
			'action/outdocx/',
			{items,},
			{responseType: 'blob',}
		)
			.then(file_resolve('out.docx'))
	},
	/*outdocx: (items) => {
		return delivery.post('action/outdocx/', {items,})
		.then(response => {
			deliveryResource.contentdownlist('list', 'delivery.docx');
			//location.replace(`${delivery.defaults.baseURL}resource/content/list/delivery.docx/`)
		})
	},*/
	default_productdelivery_set: (pk) => delivery.post(`/${pk}/action/default_productdelivery_set/`),
	deliveried: (items) => delivery.post('action/deliveried/', {items,}),
	batchupdate: (items, batchupdate) => delivery.post('action/batchupdate/', {items, batchupdate,}),
}
export const productdeliveryRest = create_rest(productdelivery);
export const proposalorderRest = create_rest(proposalorder);
export const productorderRest = create_rest(productorder);
export const proposal_description_rest = create_rest(proposal_description);
export const sourceDMRest = create_rest(sourceDM);
export const product_rest = create_rest(product);
export const paymentRest = create_rest(payment);
export const paymentResource = create_resource(payment);
export const paymentAction = {
	list: (items) => {
		return payment.post(
			'action/list/',
			{items,},
			{responseType: 'blob',}
		)
			.then(file_resolve('list.csv'))
	},
	paid: (items) => payment.post('action/paid/', {items,}),
	batchupdate: (items, batchupdate) => payment.post('action/batchupdate/', {items, batchupdate,}),
}



