<template>
	<div>
		<div class="bareBtnCtnr">
			<button
				v-if="mode=='write'"
				@click="add"
				class="btn btn-primary"
			><font-awesome-icon icon="plus" class="textAside" />新增</button>
		</div>
		<table-div
			:header="instance_header"
			:datas="instance_datas"
		>
			<template slot="id" slot-scope="props">
				<div v-if="instances[props.item].mode==='read'">
					{{ instances[props.item].product }}
				</div>
				<div v-if="instances[props.item].mode==='write'">
					<input v-model="instances[props.item].product"/>
				</div>
			</template>
			<template slot="name" slot-scope="props">
				<div v-if="instances[props.item].mode==='read'">
					<div v-for="item in product_list">
						<div v-if="item.id==instances[props.item].product">
							{{ item.name }}
						</div>
					</div>
				</div>
				<div v-if="instances[props.item].mode==='write'">
					<select v-model="instances[props.item].product">
						<option
							v-for="item in product_list"
							:value="item.id"
						>{{ item.name }}</option>
					</select>
				</div>
			</template>
			<template slot="count" slot-scope="props">
				<div v-if="instances[props.item].mode==='read'">
					{{ instances[props.item].count }}
				</div>
				<div v-if="instances[props.item].mode==='write'">
					<input v-model="instances[props.item].count"/>
				</div>
			</template>
			<template slot="action" slot-scope="props">
				<div v-if="mode=='write'">
					<button
						v-if="instances[props.item].mode==='read'"
						@click="instances[props.item].mode='write'"
						class="btn btn-primary"
					><font-awesome-icon icon="pen" class="textAside" />編輯</button>
					<button
						v-if="instances[props.item].mode==='write'"
						@click="instances[props.item].mode='read'"
						class="btn btn-primary"
					><font-awesome-icon icon="check" class="textAside" />完成</button>
					<button
						@click="remove(props.item)"
						class="btn btn-secondary"
					><font-awesome-icon icon="times" class="textAside" />刪除</button>
				</div>
			</template>
		</table-div>
		<div class="bareBtnCtnr">
			<!--<template
				v-if="mode=='write'"
			>
				<button
					v-if="pk"
					@click="operate('update')"
					class="btn btn-primary"
				><font-awesome-icon icon="sync-alt" class="textAside" />更新</button>
			</template>-->
		</div>
	</div>
</template>

<script>
import {clone} from 'lodash';
import tableDiv from './table-div.vue';
import { deliveryRest, product_rest as productRest, productdeliveryRest } from '@/api/sale';

export default {
	props: {
		pk: {
			type: Number,
			default: 0,
		},
		mode: {
			type: String,
			default: 'write',
		},
	},
	name: 'order_productdeliverys_manager',
	components: {
		tableDiv,
	},
	data() {
		return {
			product_list: [], // API 商品列表
			delivery: {
				id: 0,
				productdelivery_set: [],
			}, // API 取得 delivery
			ids: [],
			instances: [],
			instance_header: {
				id: '代號',
				name: '名稱',
				count: '數量',
				action: '動作',
			},
		};
	},
	computed: {
		instance_datas(){
			let temp = []
			this.instances.forEach((v, i)=>  {
				temp.push({
					id: i,
					name: i,
					count: i,
					action: i,
				})
			})
			return temp;
		},
		instancesUpdate(){
			let temp = []
			let old_count = this.ids.length;
			let new_count = this.instances.length
			let count = Math.max(new_count, old_count);
			for(let i=0; i<count; i++){
				if(new_count>old_count){
					if(i<old_count){
						temp.push({
							id: this.ids[i].id,
							delivery: this.pk,
							product: this.instances[i].product,
							count: this.instances[i].count,
							action: 'update',
						})
					}
					else 					if(i>=old_count){
						temp.push({
							id: null,
							delivery: this.pk,
							product: this.instances[i].product,
							count: this.instances[i].count,
							action: 'create',
						})
					}
				}
				else {
					if(i<new_count){
						temp.push({
							id: this.ids[i].id,
							delivery: this.pk,
							product: this.instances[i].product,
							count: this.instances[i].count,
							action: 'update',
						})
					}
					else 					if(i>=new_count){
						temp.push({
							id: this.ids[i].id,
							action: 'delete',
						})
					}
				}
			}
			return temp
		},
	},
	watch: {
		async pk(){
			await this.load();
		},
	},
	async mounted() {
		await this.load()
	},
	methods: {
		async load(){
			let res;
			try {
				res = await Promise.all([
					productRest.list(),
				])
				this.product_list = res[0].data;

				if(this.pk){
					res = await deliveryRest.read(this.pk);
					this.delivery = res.data;
					await this.delivery2data();
				}
			}
			catch (error) {
				console.log(error);
			}
		},

		async delivery2data(){
			let res;
			res = await this.instances_read_promise();
			this.instances = [];
			this.ids = [];
			res.forEach((v, i)=>  {
				this.instances.push({
					id: v.data.id,
					product: v.data.product,
					count: v.data.count,
					mode: 'read',
				});
			})
			res.forEach((v, i)=>  {
				this.ids.push({
					id: v.data.id,
				});
			})
		},
		add(){
			this.instances.push({
				id: null,
				product: 1,
				count: 0,
				mode: 'write',
			});
		},
		remove(index){
			this.instances.splice(index,1);
		},
		async operate(action){
			let res;
			if(action=='create'){
				try {
					res = await this.instances_create_promise();
					res = await deliveryRest.read(this.pk);
					this.delivery = res.data;
					this.delivery2data();
				}
				catch (error) {
					console.log(JSON.stringify(error.response));
				}
			}
			else if(action=='read'){
				try {
					res = await deliveryRest.read(this.pk)
					this.delivery = res.data;
					this.delivery2data();
				}
				catch (error) {
					console.log(JSON.stringify(error.response));
				}
			}
			else if(action=='update'){
				try {
					res = await this.instances_update_promise();
					res = await deliveryRest.read(this.pk);
					this.delivery = res.data;
					this.delivery2data();
					this.$root.$message.open(0, '成功更新商品資訊');
				}
				catch (error) {
					if(error.status){
						this.$root.$message.open(error.status, error.data);
					} else {
						console.log(error);
						this.$root.$message.open(-1, error);
					}
				}
			}

		},
		instances_create_promise(){
			let axiosapis = [];
			this.instances.forEach((v, i)=>  {
				v['id'] = null;
				v['delivery'] = this.pk;
				axiosapis.push(productdeliveryRest.create(v));
			})
			return Promise.all(axiosapis);
		},
		instances_read_promise(){
			let axiosapis = [];
			this.delivery.productdelivery_set.forEach((v, i)=>  {
				axiosapis.push(productdeliveryRest.read(v));
			})
			return Promise.all(axiosapis);
		},
		instances_update_promise(){
			let axiosapis = [];
			this.instancesUpdate.forEach((v, i)=>  {
				if(v.action==='create'){
					axiosapis.push(productdeliveryRest.create(v));
				}
				else if(v.action==='update'){
					axiosapis.push(productdeliveryRest.update(v.id, v));
				}
				else if(v.action==='delete'){
					axiosapis.push(productdeliveryRest.delete(v.id));
				}
			})
			return Promise.all(axiosapis);
		},
	},
};
</script>

<style lang="scss">
</style>



