import Vue from 'vue';
import Router from 'vue-router';

import sale_home from '@/sale/home';
import auth from '@/sale/auth';
import donation from '@/sale/donation';
import purchase from '@/sale/purchase';
import list from '@/sale/list';
import deliveryList from '@/sale/delivery_list';
import paymentList from '@/sale/payment_list';
import manage from '@/sale/manage';
import sign_up from '@/sale/sign_up';
import login from '@/sale/login';
import resetPassword from '@/sale/reset_password';

Vue.use(Router);

const getAbsolutePath = () => {
	let path = location.pathname
	return path.substring(0, path.lastIndexOf('/') + 1)
}

export default new Router({
	mode: 'history',
	base: '/vatsale',
	routes: [
		{
			path: '/',
			component: sale_home,
			children: [
				{
					path: '',
					name: 'root',
					component: list,
				},
				{
					path: 'auth',
					name: 'auth',
					component: auth,
				},
				{
					path: 'purchase',
					name: 'purchase',
					component: purchase,
				},
				{
					path: 'donation/',
					name: 'donation',
					component: donation,
				},
				{
					path: 'list/',
					name: 'list',
					component: list,
				},
				{
					path: 'payment_list/',
					name: 'payment_list',
					component: paymentList,
				},
				{
					path: 'delivery_list/',
					name: 'delivery_list',
					component: deliveryList,
				},
				{
					path: 'manage/:id',
					name: 'manage',
					component: manage,
				},
				{
					path: 'sign_up/',
					name: 'sign_up',
					component: sign_up,
				},
				{
					path: 'login/',
					name: 'login',
					component: login,
				},
				{
					path: 'reset_password/',
					name: 'reset_password',
					component: resetPassword,
				},
			],
		},
	]
});



