<template>
	<div>
		<h2>訂單列表</h2>
		<filters
			:filter_field="instance_filter_field"
			@ok="search"
			ref="fc"
		></filters>
		<div class="btn-group paddTopBtmSm the4btn">
			<button class="btn btn-primary" @click="select_all"><font-awesome-icon icon="check" class="textAside" />全選</button>
			<button class="btn btn-primary" @click="select_items=[]"><font-awesome-icon icon="times" class="textAside" />全不選</button>
			<button class="btn btn-primary" @click="select_inv"><font-awesome-icon icon="exchange-alt" class="textAside" />反向選</button>
			<button class="btn btn-primary" @click="$refs['fc'].open();"><font-awesome-icon icon="filter" class="textAside" />查詢條件</button>
		</div>
		<table-div
			:header="instance_header"
			:datas="instance_datas"
		>
			<template slot="id" slot-scope="props">
				<label>
					<input type="checkbox" v-model="select_items" :value="props.item" class="visu_verticalAlign">
					<span class="visu_verticalAlign">{{ props.item }}</span>
				</label>
				<!-- ."序號" field, number and checkbox. -->
			</template>
			<template slot="status" slot-scope="props">
				<div v-if="props.item==0">
					資料已建立
				</div>
				<div v-else-if="props.item==1">
					資料已確認
				</div>
				<div v-else-if="props.item==2">
					發票或收據已開立
				</div>
			</template>
			<template slot="action" slot-scope="props">
				<div class="paddTopBtmSm">
					<button
						@click="$router.push({ name: 'manage', params: { id: props.item }})"
						class="btn btn-primary"
					>管理</button>
				</div>
			</template>
		</table-div>
		<div>
			<select
				v-model="action"
				class="custom-select"
			>
				<option value="batch_update">批次更新資料</option>
			</select>
			<div class="bareBtnCtnr goBtnCtnr">
				<button
					@click="acton"
					class="btn btn-primary"
				>GO !</button>
			</div>
		</div>
		<batch-update
			:fields="instance_batch_fields"
			@ok="batchupdate"
			ref="bu"
		></batch-update>
	</div>
</template>

<script>
import { Token, token } from '@/utils/token';
import { order_rest as orderRest, orderAction, } from '@/api/sale';
import bsModal from './modal.vue';
import tableDiv from './table-div.vue';
import batchUpdate from './batch_update.vue';
import filters from './filter.vue';

export default {
	components: {
		bsModal,
		tableDiv,
		batchUpdate,
		filters,
	},
	data() {
		return {
			action: 'batch_update',
			instance_filter_field: [
				{
					key: 'create_date',
					type: 'date',
					label: '日期',
				},
				{
					key: 'name',
					type: 'string',
					label: '購買者姓名',
				},
				{
					key: 'receipt',
					type: 'boolean',
					label: '購買或捐款(是為購買；否為捐款)',
				},
				{
					key: 'status',
					type: 'select',
					label: '狀態',
					'choices' : [
						{
							'value': '0',
							'display_name': '資料已建立',
						},
						{
							'value': '1',
							'display_name': '資料已確認',
						},
						{
							'value': '2',
							'display_name': '發票或收據已開立',
						},
					],
				},
			],
			instance_batch_fields: [
				{
					key: 'create_date',
					type: 'date',
					label: '日期',
				},
				{
					key: 'status',
					type: 'select',
					label: '狀態',
					'choices' : [
						{
							'value': '0',
							'display_name': '資料已建立',
						},
						{
							'value': '1',
							'display_name': '資料已確認',
						},
						{
							'value': '2',
							'display_name': '發票或收據已開立',
						},
					],
				},
			],
			instances: [],
			instance_datas: [],
			instance_header: {
				id: '訂單序號',
				name: '購買者姓名',
				create_date: '訂單建立日期',
				status: '狀態',
				action: '動作',
			},
			select_items: [],
		};
	},
	async mounted() {
		await this.load();
	},
	methods: {
		async load(){
			let res;
			try {
				res = await orderRest.list();
				this.instances = res.data;
				this.instance_datas = [];
				this.instances.forEach((v, i)=>  {
					this.instance_datas.push({
						id: v.id,
						name: v.name,
						create_date: v.create_date,
						status: v.status,
						action: v.id,
					})
				})
			}
			catch (error) {
				console.log(error);
			}
		},
		async acton(){
			let res;
			if(this.action==='batch_update'){
				this.$refs['bu'].open();
			}
		},
		async batchupdate(event){
			let res;
			try {
				res = await orderAction.batchupdate(this.select_items, event);
				await this.load();
				this.$root.$message.open(200, '成功批次更新資料');
			}
			catch (error) {
				if(error.response.status){
					this.$root.$message.open(error.response.status, error.response.data);
				} else {
					console.log(error);
					this.$root.$message.open(-1, error);
				}
			}
		},
		async search(event){
			let res;
			try {
				res = await orderRest.filter(event);
				this.$root.$message.open(0, '成功查詢資料' +res.data.length, '');
				this.instances = res.data;
				this.instance_datas = [];
				this.instances.forEach((v, i)=>  {
					this.instance_datas.push({
						id: v.id,
						name: v.name,
						create_date: v.create_date,
						status: v.status,
						action: v.id,
					})
				})
			}
			catch (error) {
				console.log(error);
				this.$root.$message.open(1, error.response, '');
			}
		},
		select_all(){
			this.instance_datas.forEach((v, i)=>  {
				this.select_items.push(v.id)
			})
		},
		select_inv(){
			let temp = []
			this.instance_datas.forEach((v, i)=> {
				if(!_.includes(this.select_items, v.id)){
					temp.push(v.id);
				}
			})
			this.select_items = temp;
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';

	.visu_verticalAlign {
		vertical-align: middle;
	}
	.the4btn {
		padding-top: $spaceS5;
		padding-bottom: $spaceS3;
	}
</style>



