<template>
	<div>
		<bs-modal id="confirm" ref="confirm">
			<template slot="header">
				<h4 class="modal-title">系統訊息</h4>
				<button class="close" @click="cancel" aria-label="Close"><span>&times;</span></button>
			</template>
			<template slot="body">
				<div v-if="type=='html'" v-html="data"></div>
				<div v-else-if="type=='array'">
					<div v-for="(item, index) in data">
						{{ index+1 }}. {{ item }}
					</div>
				</div>
				<div v-else-if="type=='object'">
					<div v-for="(value, key, index) in data">
						{{ index }}. {{ key }} : {{ value }}
					</div>
				</div>
				<div v-else>{{ data }}</div>
			</template>
			<template slot="footer">
				<button class="btn btn-primary" @click="cancel">取消</button>
				<button class="btn btn-primary" @click="ok">確定</button>
			</template>
		</bs-modal>
	</div>
</template>

<script>
import bsModal from './modal.vue';
import { isString, isArray, isObject } from '@/utils/type';

export default {
	props: {
	},
	name: 'confirm',
	components: {
			bsModal,
	},
	data() {
		return {
			status: 0, // 200 success 400 api error 500 server error
			data: '',
			type: 'string',
		};
	},
	created() {
		this.$root.$confirm = this;
	},
	methods: {
		open(status, data, type){
			this.status = status;
			if(!type){
				if(isString(data)) {
					this.data = data;
					this.type = 'string';
				}
				else if(isArray(data)) {
					this.data = data;
					this.type = 'array';
				}
				else if(isObject(data)) {
					this.data = data;
					this.type = 'object';
				}
				else {
					this.data = JSON.stringify(data);
					this.type = 'string';
				}
			}
			this.$refs['confirm'].open('');
			return new Promise((resolve, reject) => {
				this.$on('cancel', () => {
					console.log('Cancel!');
					reject();
				});
				this.$on('ok', () => {
					console.log('Ok!');
					resolve();
				});
			} );
		},
		cancel(){
			this.$refs['confirm'].close();
			this.$emit('cancel');
		},
		ok(){
			this.$refs['confirm'].close();
			this.$emit('ok');
		},
	},
};
</script>

<style lang="scss">
</style>



