<template>
	<div>
		<h2>商品選購</h2>
		<div class="scrollspyNavAndSection">
			<scrollspy
				:navLinkIdAndName="[
					{ id: 'basicInfo', name: '基本', },
					{ id: 'editProposalAmount', name: '方案數量編輯', },
					{ id: 'editProductAmount', name: '商品數量編輯', },
				]"
			></scrollspy>
			<div class="scrollspySection" id="basicInfo">
				<h3>基本</h3>
				<order-create ref="oc"></order-create>
			</div>
			<div class="scrollspySection" id="editProposalAmount">
				<h3>方案數量編輯</h3>
				<proposal-orders-manager ref="pp"></proposal-orders-manager>
			</div>
			<div class="scrollspySection" id="editProductAmount">
				<h3>商品數量編輯</h3>
				<product-orders-manager ref="pd"></product-orders-manager>
			</div>
		</div>
		<div class="bareBtnCtnr">
			<button
				@click="create"
				class="btn btn-primary"
			><font-awesome-icon icon="sync-alt" class="textAside" />建立</button>
		</div>
	</div>
</template>

<script>
import { order_rest as orderRest, orderAction, deliveryAction, } from '@/api/sale';
import tab from './tab.vue';
import orderCreate from './order_create.vue';
import productOrdersManager from './product_orders_manager.vue';
import proposalOrdersManager from './proposal_orders_manager.vue';
import scrollspy from './scrollspy.vue';

export default {
	components: {
		tab,
		orderCreate,
		productOrdersManager,
		proposalOrdersManager,
		scrollspy,
	},
	data() {
		return {
			pk: null,
		};
	},
	mounted () {
	},
	computed: {
	},
	methods: {
		async create(){
			let res;
			try {
				res = await this.$refs['oc'].create()
				this.pk= res.data.id;
				let order = res.data;

				this.$refs['pd'].pk = this.pk;
				res = await this.$refs['pd'].operate('create')

				this.$refs['pp'].pk = this.pk;
				res = await this.$refs['pp'].operate('create')

				let installment = this.$refs['oc'].input['installment']
				await orderAction.default_payment(this.pk, installment);

				//default_productdelivery_set
				let deliverys = order.delivery_set;
				if(deliverys.length==1){
					res = await deliveryAction.default_productdelivery_set(deliverys[0]);
				}

				this.$router.push({ name: 'manage', params: { id: this.pk }});

				this.$root.$message.open(0, '成功建立訂單' +this.pk, '');
			}
			catch (error) {
				if(error.status){
					this.$root.$message.open(error.status, error.data);
				} else {
					console.log(error);
					this.$root.$message.open(-1, error);
				}
			}
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';

	// .Useless.
