	///deep/ .purchTabsCtnr ul {
	//	border-bottom: 1px solid $clorGreyBright;
	//	padding-bottom: 2px;
	//	margin-top: calc(0em - #{$paddingTopBtmDefault});
	//	// ."Move up" the whole Tabs through negative margin for aesthetic reason.
	//}
	///deep/ .purchTabsCtnr li {
	//	display: inline-block;
	//	border-style: solid;
	//	border-color: $clorGreyBright;
	//	border-width: 1px 1px 0px 1px;
	//	border-radius: 3px 3px 0 0;
	//	padding: 2px 1.1em;
	//	line-height: 110%;
	//}
	///deep/ .purchTabsCtnr li:first-child {
	//	margin-left: 0.8em;
	//}
	///deep/ .purchTabsCtnr li:hover {
	//	background-color: $clorBtnHoverGrey;
	//	border-color: $clorGreyBright;
	//}
//
	//@media screen and (max-width: $screenMdWidth) {
	//	/deep/ .purchTabsCtnr li {
	//		padding: 5px 2em;
	//		line-height: 110%;
	//	}
	//}
</style>



