import axios from 'axios';
import { create_rest, host } from './base.js'
import { set_token, } from './token.js'

const user = axios.create({
	baseURL: host +'user/api/users/'
});

/*order.interceptors.request.use(set_token, error => {
	// Do something with request error
	return Promise.reject(error);
});*/

export const userRest = create_rest(user);
export const userAction = {
	reset_password: (username, birthday) => user.post('action/reset_password/', {username, birthday,}),
}



