import axios from 'axios';
import jwt_decode from 'jwt-decode'
import { host } from '@/api/base'

const o2j = (v) => {
	//物件轉json文字

	let c = '';
	try {
		c = JSON.stringify(v, null, '\t');
	}
	catch (err) {
	}
	return c;
}

const j2o = (v) => {
	//json文字轉物件

	let c = {};
	try {
		c = JSON.parse(v);
	}
	catch (err) {
	}
	return c;
}

const tokenaxios = axios.create({
	baseURL: host +'api/token/'
});

export let Token = {
	name: '',
	/*token: {
		access: '',
		refresh: '',
	},*/
	set(name){
		this.name = name;
	},
	get(){
		return this.token;
	},
	load(){
		try {
			this.token = JSON.parse(localStorage.getItem(this.name));
		} catch(error){
			this.token = {
				access: null,
				refresh: null,
			}
		}
	},
	save(){
		localStorage.setItem(this.name, JSON.stringify(this.token));
	},
	remove(){
		localStorage.removeItem(this.name);
	},
	check_token(type){
		try {
			let token_obj = jwt_decode(this.token[type]);
			return true;
		}
		catch (err) {
			return false;
		}
	},
	get_exp_countdown(type){
		try {
			let token_obj = jwt_decode(this.token[type]);
			let exp_date;
			if(type=='access'){
				exp_date = this.token.access_obtain_time + 300000
			}
			else if(type=='refresh'){
				exp_date = this.token.refresh_obtain_time + 86400000
			}
			return exp_date - Date.now()
		}
		catch (err) {
			return null;
		}
	},
	check_exp(type){
		try {
			let exp_second = this.get_exp_countdown(type)/1000;
			if(exp_second > 0){
				return false;
			}
			else {
				return true;
			}
		}
		catch (err) {
			console.log(err);
			return false;
		}
	},
	check_exp_come_soon(type, period_second){
		try {
			let exp_second = this.get_exp_countdown(type)/1000;
			if(exp_second > 0 && exp_second - period_second < 0){
				return true;
			}
			else {
				return false;
			}
		}
		catch (err) {
			return false;
		}
	},
	check_active(type){
		try {
			let exp_second = this.get_exp_countdown(type)/1000;
			if(exp_second > 1){
				return true;
			}
			else {
				return false;
			}
		}
		catch (err) {
			return false;
		}
	},
	get_all(type){
		try {
			return jwt_decode(this.token[type]);
		}
		catch (err) {
			return null;
		}
	},
	get_user_id(type){
		try {
			return jwt_decode(this.token[type]).user_id;
		}
		catch (err) {
			return null;
		}
	},
	get_exp_date(type){
		try {
			return new Date(jwt_decode(this.token[type]).exp*1000);
		}
		catch (err) {
			return null;
		}
	},
	refresh() {
		return tokenaxios.post('refresh/', {
			refresh: this.token.refresh,
		})
			.then(res => {
				this.token.access = res.data.access;
				this.token.access_obtain_time = Date.now()
				this.save();
			})

	},
	obtain(username, password) {
		return tokenaxios.post('', {
			username,
			password,
		})
			.then(res => {
				this.token = res.data;
				this.token.access_obtain_time = Date.now()
				this.token.refresh_obtain_time = Date.now()
				this.save();
			})
	},
}

let t = Object.create(Token);
t.set('token');
t.load();

export const token = t;



