<template>
	<div>
		<h2>立即捐款</h2>
		<order-create ref="oc"></order-create>
		<formdrf
			:model_info="model.amount"
			:field="'amount'"
			:offset-class="'col-sm-offset-3'"
			v-model="amount"
			@keyup.enter.native=""
		></formdrf>
		<div class="paddTopBtmSm">
			<button
				@click="create"
				class="btn btn-primary"
			><font-awesome-icon icon="sync-alt" class="textAside" />完整建立</button>
		</div>
	</div>
</template>

<script>
import formdrf from './form.vue';
import { order_rest as orderRest, orderAction, proposalorderRest } from '@/api/sale';
import orderCreate from './order_create.vue';

export default {
	components: {
		formdrf,
		orderCreate,
	},
	data() {
		return {
			amount: 1000,
			model: {
				amount: {
					'label': '金額',
					'type': 'text',
					required: true,
				},
			},
		};
	},
	mounted() {
		this.$refs['oc'].input.receipt=false;
	},
	methods: {
		async create(){
			let res;
			try {
				res = await this.$refs['oc'].create()

				let pk= res.data.id;
				res = await proposalorderRest.create({
					proposal_description: 1,
					order: pk,
					amount: this.amount,
					count: 1,
				});
				await orderAction.default_payment(pk, 'false');

				this.$router.push({ name: 'manage', params: { id: pk }});
				this.$root.$message.open(0, '成功建立訂單' +pk);

			}
			catch (error) {
				if(error.status){
					this.$root.$message.open(error.status, error.data);
				} else {
					console.log(error);
					this.$root.$message.open(-1, error);
				}
			}
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';
</style>



