<template>
	<header>
		<nav class="navbar navbar-expand-md navbar-light bg-light">
			<button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
				<span class="navbar-toggler-icon"></span>
			</button>
		<div class="collapse navbar-collapse" id="navbarSupportedContent">
			<ul class="navbar-nav">
				<li class="nav-item">
					<router-link :to="{name: 'auth'}" class="nav-link">認證資訊</router-link>
				</li>
				<li class="nav-item dropdown">
					<a href="#" class="nav-link dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false" >新增訂單</a>
					<div class="dropdown-menu">
						<router-link :to="{name:'purchase'}" class="dropdown-item">商品選購</router-link>
						<router-link :to="{name: 'donation'}" class="dropdown-item">立即捐款</router-link>
						<!--<div class="dropdown-divider"></div>
						<a class="dropdown-item" href="#">More Link ...</a>
						<a class="dropdown-item" href="#">More Link ...</a>-->
					</div>
				</li>
				<li class="nav-item">
					<router-link :to="{name:'list'}" class="nav-link">訂單列表</router-link>
				</li>
				<li class="nav-item">
					<router-link :to="{name:'payment_list'}" class="nav-link">付款列表</router-link>
				</li>
				<li class="nav-item">
					<router-link :to="{name:'delivery_list'}" class="nav-link">出貨列表</router-link>
				</li>
				<template v-if="token.check_exp('refresh')">
					<li class="nav-item">
						<!--<router-link :to="{name: 'sign_up'}" class="nav-link">註冊</router-link>-->
					</li>
					<li class="nav-item">
						<router-link :to="{name: 'login'}" class="nav-link">登錄</router-link>
					</li>
				</template>
				<template v-else>
					<li class="nav-item">
						<a @click="logout" class="nav-link" href="#">登出</a>
					</li>
				</template>
			</ul>
		</div>
		</nav>
		<!--<div>到期：{{ token.check_exp('refresh') }}</div>-->
	</header>
</template>

<script>
import { token } from '@/utils/token';

export default {
	data() {
		return {
			token: token,
		};
	},
	computed: {
	},
	methods: {
		logout(){
			this.$root.$token.token.remove();
			this.$root.$token.token.load();
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';

</style>



