<template>
	<nav class="scrollspyNav">
		<ul class="nav nav-pills">
			<li class="nav-item" v-for="(idAndName, idx) in navLinkIdAndName">
				<a :class="['nav-link', (idx === 0 ? 'active' : '')]" :href="`#${idAndName.id}`">{{ idAndName.name }}</a>
			</li>
		</ul>
		<div class="visuStuffingBgroundGap">
			<!-- Pure trivial visual things/fix. -->
		</div>
	</nav>
</template>

<script>
// .Purpose: Make "scrollspy" effect. Bootstrap has scrollspy effect, but it need to
// applying html class on <body> elements, so it is not preferable.
// .Rules: You must structure your code like this:
// <div class="myNameForSpyNavAndSection">
//   <nav class="customClassNameForScrollspyNav">
//     <ul>
//       <li><a href="#apple">Apple...</a></li>
//       <li><a href="#bear">Bear...</a></li>
//       <li><a href="#cat">Cat...</a></li>
//     </ul>
//   </nav>
//   <div id="apple" class="myNameForScrollspySection">...</div>
//   <div id="bear" class="myNameForScrollspySection">...</div>
//   <div id="cat" class="myNameForScrollspySection">...</div>
// </div>
// otherwise it will not work.
// .Require: jQeury, bootstrap. It is fine if your don't want to remove bootstrap
// , just modify/update the styling to keep it looking good.
import $ from 'jquery';

export default {
	props: [
		"navLinkIdAndName",
	],
	mounted () {
		this.setSpyData();
		this.onNavLinkClick();
		this.setSpyScroll();
		this.onResize();
		this.listenVisibleState();
	},
	data(){
		return {
			visibleState: false,
			hClassScrollspyNav: "scrollspyNav",
			// .HTML class name of the <nav> element you want to apply this "scrollspy" effect.
			elmScrollspyNav: null,
			hClassScrollspySection: "scrollspySection",
			// .HTML class name of the section container you want to apply this "scrollspy" effect.
			offset_allScrollspySection: null,
			offsetMin_scrollspySection: null,
			offsetMax_scrollspySection: null,
			offsetDetectDeviation: 110,
			// .Purpose: Prevent some glitchy.
			// .About: Normally, if ${window}.scrollTop() is in the range of one of
			// "scrollspySection" element, the corresponding "scrollspyNav a" will become "focused".
			// But this will not be exactly, here I will set a little deviation,
			// either make the deviation a bit higher or lower, so when user click on
			// scrollspyNav a, the ${window}.scrollTop() will not placed exactly onto
			// the top of "scrollspySection" border which makes the result glitchy.
			offsetNavScrollDeviation: 10,
			// .Purpose: Work with offsetDetectDeviation. This value have to be positive,
			// and no need too much otherwise the top of "scrollspySection" will be overlap
			// by scrollspy nav, visually.
			itvId: null,
			// .A place use setInterval, and it need to be destroyed too when this
			// component is destroyed.
		}
	},
	methods: {
		onSpyScroll () {
			var crrScrollTop = $(window).scrollTop();
			findFittestRange:
			if (crrScrollTop > this.offsetMin_scrollspySection &&
				crrScrollTop < this.offsetMax_scrollspySection) {
				for (let i = 0 ; i < this.offset_allScrollspySection.length ; i++) {
					if (i !== this.offset_allScrollspySection.length - 1) {
						if (crrScrollTop > this.offset_allScrollspySection[i].top &&
						crrScrollTop < this.offset_allScrollspySection[i + 1].top) {
								this.updateCrrNavItm (this.offset_allScrollspySection, i, this.elmScrollspyNav);
								break findFittestRange;
						}
					} else {
						if (crrScrollTop > this.offset_allScrollspySection[i].top &&
						crrScrollTop < this.offset_allScrollspySection[i].btm) {
							this.updateCrrNavItm (this.offset_allScrollspySection, i, this.elmScrollspyNav);
							break findFittestRange;
						}
					}
				}
			}
		},
		getElmOffsetTopById (id, offset_allScrollspySection) {
			for (let elm of offset_allScrollspySection) {
				if (elm.id === id) {
					return elm.top;
				}
			}
		},
		listenVisibleState () {
			// .Purpose: Keep "detecting" if "ScrollspyNav" is visible on screen or not.
			// If the tabs changes, vue automatically make the whole tab invisible
			// (display: none), and I need to write some logic to deal with this.
			function detectIfNavIsVisible (elmScrollspyNav) {
				return (elmScrollspyNav.get(0).offsetHeight > 0);
				// .0 for hidden, more than 0 for displaying.
			}

			// .Terrible solution: This approach is use "setInterval" to keep "detecting"
			// if "ScrollspyNav" is visible on screen or not, in a time interval.
			// Since I can't found better way to "detect" if
			// "elmScrollspyNav.get(0).offsetHeight" (browser computed property)
			// have been changed or not, I will still use this approach.
			this.itvId = setInterval(() => {
				var crrVisState = detectIfNavIsVisible(this.elmScrollspyNav);
				var prevVisState = this.visibleState;
				var crrVisStateNum = (crrVisState === true ? 1 : 0);
				var prevVisStateNum = (prevVisState === true ? 1 : 0);
				if (crrVisStateNum - prevVisStateNum > 0) {
					// .Purpuse: Only if state of "crrVisState/prevVisState" is "1/0",
					// then do something, otherwise ("1/1", "0/1", "0/0") do nothihng.
					this.setSpyData();
				}
				if (prevVisState !== crrVisState) {
					this.visibleState = crrVisState;
				}
			}, 500);
		},
		onResize () {
			$(window).resize(() => {
				this.setSpyData();
				this.onSpyScroll();
			});
		},
		onNavLinkClick () {
			var navLink = this.elmScrollspyNav.find("ul li a");
			navLink.click((e) => {
				e.preventDefault();
				$(window).scrollTop(
					this.getElmOffsetTopById(
						e.target.getAttribute("href").substr(1),
						this.offset_allScrollspySection
					) + this.offsetNavScrollDeviation
				);
			});
		},
		removeSpyScroll () {
			// .Remove scroll event listener on "window" object.
			removeEventListener("scroll", this.onSpyScroll);
		},
		setSpyScroll () {
			// .Set scroll event listener on "window" object, everytime user scrolls the page,
			// update the "focused" <nav> <li>.
			addEventListener("scroll", this.onSpyScroll);
		},
		setSpyData () {
			// .Calculate required positioning information for later use. Only invoked once
			// after the page loaded.
			this.offsetMin_scrollspySection = null;
			this.offsetMax_scrollspySection = 0;
			this.offset_allScrollspySection = [];
			//this.elmScrollspyNav = document.getElementsByClassName(this.hClassScrollspyNav)[0];
			this.elmScrollspyNav = $("." + this.hClassScrollspyNav);
			$("." + this.hClassScrollspySection).each((idx, elm) => {
				this.offset_allScrollspySection.push({
					id: $(elm).attr('id'),
					top: $(elm).offset().top - this.offsetDetectDeviation,
					btm: $(elm).offset().top + $(elm).innerHeight() - this.offsetDetectDeviation,
				});
			});
			for(let elm of this.offset_allScrollspySection) {
				let crrTop = elm.top;
				let crrBtm = elm.btm;
				this.offsetMin_scrollspySection = (
					this.offsetMin_scrollspySection === null ?
					crrTop :
					this.offsetMin_scrollspySection
				);
				this.offsetMin_scrollspySection = (
					crrTop < this.offsetMin_scrollspySection ?
					crrTop :
					this.offsetMin_scrollspySection
				);
				this.offsetMax_scrollspySection = (
					crrBtm > this.offsetMax_scrollspySection ?
					crrBtm :
					this.offsetMax_scrollspySection
				);
			}
			// .Try to get the smallest top from "offset_allScrollspySection[key].top",
			// and biggest buttom "offset_allScrollspySection[key].btm".
		},
		updateCrrNavItm (offset_allScrollspySection, idx, elmScrollspyNav) {
			var scrollspyNavLinks = elmScrollspyNav.find("ul li");
			scrollspyNavLinks.each((idxLink, li) => {
				if (li.querySelector("a").getAttribute("href").substr(1) ===
				offset_allScrollspySection[idx].id) {
					li.classList.add("crrSpy");
					li.querySelector("a").classList.add("active");
					// .Class "active" is served for bootstrap.
					return;
				}
				li.classList.remove("crrSpy");
				li.querySelector("a").classList.remove("active");
				// .Class "active" is served for bootstrap.
			});

			//loopToMatchRightAnchor:
			//for (let li of scrollspyNavLinks) {
			//	if (li.querySelector("a").getAttribute("href").substr(1) ===
			//	offset_allScrollspySection[idx].id) {
			//		li.classList.add("crrSpy");
			//		continue loopToMatchRightAnchor;
			//	}
			//	li.classList.remove("crrSpy");
			//}
		},
	},
	beforeDestroy () {
		clearInterval(this.itvId);
		this.removeSpyScroll();
	},
}
</script>
<style scoped lang="scss">
	@import '../style/base.scss';

	$clorNavBground: white;
	.scrollspyNav {
		position: sticky;
		top: 0;
		//background-color: grey;
		z-index: 2;
		// .If I set to 1, it works at almost all places, but surprisingly a
		//special element on the page have higher z-index. So I increase it to 2.
		padding: $spaceS3 $spaceS3;
		background-color: $clorNavBground;
		// .This in fact do nothing, since parent layer will be overlap by all child layer,
		// , even for child that has minus z-index.
	}
	.scrollspyNav ul {
		background-color: $clorNavBground;
	}
	.scrollspyNav .visuStuffingBgroundGap {
		position: absolute;
		top: 0;
		bottom: 0;
		left: -50em;
		right: -50em;
		// .Set a very big "expanding" to make sure it is long enough to be bigger
		// and overflow the screen width.
		z-index: -10;
		background-color: $clorNavBground;
		box-shadow: 0 7px 4px -4px rgba(0, 0, 0, 0.05);
		// .Add a shadow line to buttom border.
	}
	.scrollspyNav a {
		//color: black;
	}
	.scrollspyNav li.crrSpy a {
		//color: white;
	}
</style>



