<template>
	<div>
		<h2>訂單管理</h2>
		<tab :headinglevel="3" :data="tab_data">
			<template slot="base" slot-scope="props">
				<order-manager :pk="Number($route.params.id)"></order-manager>
			</template>
			<template slot="advanced" slot-scope="props">
				<div class="scrollspyNavAndSection">
					<scrollspy
						:navLinkIdAndName="[
							{ id: 'productOrders', name: '商品數量', },
							{ id: 'deliverys', name: '出貨資訊', },
							{ id: 'paymentOrders', name: '付款資訊', },
							{ id: 'proposalOrders', name: '方案資訊', },
						]"
					></scrollspy>
					<div class="scrollspySection" id="productOrders">
						<h3>商品數量</h3>
						<product-orders-manager :pk="Number($route.params.id)"></product-orders-manager>
					</div>
					<div class="scrollspySection" id="deliverys">
						<h3>出貨資訊</h3>
						<deliverys-manager :pk="Number($route.params.id)"></deliverys-manager>
					</div>
					<div class="scrollspySection" id="proposalOrders">
						<h3>方案資訊</h3>
						<proposal-orders-manager
							:pk="Number($route.params.id)"
							@update="load_payment_orders_manager"
							ref="proposal_om"
						></proposal-orders-manager>
					</div>
					<div class="scrollspySection" id="paymentOrders">
						<h3>付款資訊</h3>
						<payment-orders-manager
							:pk="Number($route.params.id)"
							@update="load_proposal_orders_manager"
							ref="payment_om"
						></payment-orders-manager>
					</div>
				</div>
			</template>
		</tab>
	</div>
</template>

<script>
import tableDiv from './table-div.vue';
import tableSplit from './table_split.vue';
import tab from './tab.vue';
import { order_rest as orderRest, } from '@/api/sale';
import orderManager from './order_manager.vue';
import deliverysManager from './deliverys_manager.vue';
import productOrdersManager from './product_orders_manager.vue';
import proposalOrdersManager from './proposal_orders_manager.vue';
import paymentOrdersManager from './payment_orders_manager.vue';
import scrollspy from './scrollspy.vue';

export default {
	components: {
		tableDiv,
		tableSplit,
		tab,
		orderManager,
		deliverysManager,
		paymentOrdersManager,
		productOrdersManager,
		proposalOrdersManager,
		scrollspy,
	},
	data() {
		return {
			proposal_description_list: [], // API 案描述列表
			product_list: [], // API 商品列表
			order: {},
			deliverys: [],
			proposals: [],
			tab_data: [
				{
					display_name: '基本',
					value: 'base',
					type: 'base',
					data: {},
				},
				{
					display_name: '進階',
					value: 'advanced',
					type: 'advanced',
					data: {},
				},
			],
			product_count_header:{
				name: '名稱',
				count: '數量',
			},
			date_amount_header:{
				date: '日期',
				amount: '金額',
			},
		};
	},
	computed: {

	},
	async mounted() {
		let res;
		try {
			res = await Promise.all([
				orderRest.read(this.$route.params.id),
			])
			this.order = res[0].data;
			await this.load();
		}
		catch (error) {
			console.log(error);
		}
	},
	methods: {
		async load(){
		},
		async load_proposal_orders_manager(){
			await this.$refs['proposal_om'].load();
		},
		async load_payment_orders_manager(){
			await this.$refs['payment_om'].load();
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';
</style>



