<template>
	<div>
		<div class="bareBtnCtnr">
			<button
				@click="add"
				class="btn btn-primary"
			><font-awesome-icon icon="plus" class="textAside" />新增</button>
		</div>
		<table-div
			:header="payment_header"
			:datas="payment_datas"
		>
			<template slot="date" slot-scope="props">
				<div v-if="payments[props.item].mode==='read'">
					{{ payments[props.item].date }}
				</div>
				<div v-if="payments[props.item].mode==='write'">
					<input v-model="payments[props.item].date"/>
				</div>
			</template>
			<template slot="amount" slot-scope="props">
				<div v-if="payments[props.item].mode==='read'">
					{{ payments[props.item].amount }}
				</div>
				<div v-if="payments[props.item].mode==='write'">
					<input v-model="payments[props.item].amount"/>
				</div>
			</template>
			<template slot="paid" slot-scope="props">
				<div v-if="payments[props.item].mode==='read'">
					<div v-if="payments[props.item].paid===true">是</div>
					<div v-if="payments[props.item].paid===false">否</div>
				</div>
				<div v-if="payments[props.item].mode==='write'">
					<input v-model="payments[props.item].paid" type="checkbox" />
				</div>
			</template>
			<template slot="action" slot-scope="props">
				<div class="bareBtnCtnr">
					<button
						v-if="payments[props.item].mode==='read'"
						@click="payments[props.item].mode='write'"
						class="btn btn-primary"
					><font-awesome-icon icon="pen" class="textAside" />編輯</button>
					<button
						v-if="payments[props.item].mode==='write'"
						@click="payments[props.item].mode='read'"
						class="btn btn-primary"
					><font-awesome-icon icon="check" class="textAside" />完成</button>
					<button
						@click="remove(props.item)"
						class="btn btn-secondary"
					><font-awesome-icon icon="times" class="textAside" />刪除</button>
				</div>
			</template>
		</table-div>
		<div class="paddTopBtmSm ctnr_priceAmount">
			<div>付款金額：{{ total_amount }}</div>
			<div>方案金額：{{ order.proposal_price }}</div>
			<button
				@click="operate('update')"
				class="btn btn-primary"
			><font-awesome-icon icon="sync-alt" class="textAside" />更新</button>
		</div>
	</div>
</template>

<script>
import {clone} from 'lodash';
import { Date2String, } from '@/utils/date';
import bsModal from './modal.vue';
import tableDiv from './table-div.vue';
import { order_rest as orderRest, paymentRest } from '@/api/sale';

export default {
	props: {
		pk: {
			type: Number,
			default: 0,
		},
	},
	name: 'order_payments_manager',
	components: {
			bsModal,
		tableDiv,
	},
	data() {
		return {
			order: {
				id: 0,
				payment_set: [],
			}, // API 取得 order
			ids: [],
			payments: [],
			payment_header: {
				order: '期數',
				date: '日期',
				'amount': '金額',
				paid: '支付',
				action: '動作',
			},
		};
	},
	computed: {
		total_amount(){
			let price=0;
			this.payments.forEach((v, i)=>{
				price = price +Number(this.payments[i].amount)
			})
			return price;
		},
		payment_datas(){
			let temp = []
			this.payments.forEach((v, i)=>  {
				temp.push({
					order: i,
					date: i,
					amount: i,
					paid: i,
					action: i,
				})
			})
			return temp;
		},
		paymentsUpdate(){
			let temp = []
			let old_count = this.ids.length;
			let new_count = this.payments.length
			let count = Math.max(new_count, old_count);
			for(let i=0; i<count; i++){
				if(new_count>old_count){
					if(i<old_count){
						temp.push({
							id: this.ids[i].id,
							order: i,
							date: this.payments[i].date,
							amount: this.payments[i].amount,
							paid: this.payments[i].paid,
							orderobj: this.pk,
							action: 'update',
						})
					}
					else 					if(i>=old_count){
						temp.push({
							id: null,
							order: i,
							date: this.payments[i].date,
							amount: this.payments[i].amount,
							paid: this.payments[i].paid,
							orderobj: this.pk,
							action: 'create',
						})
					}
				}
				else {
					if(i<new_count){
						temp.push({
							id: this.ids[i].id,
							order: i,
							date: this.payments[i].date,
							amount: this.payments[i].amount,
							paid: this.payments[i].paid,
							orderobj: this.pk,
							action: 'update',
						})
					}
					else 					if(i>=new_count){
						temp.push({
							id: this.ids[i].id,
							order: null,
							date: null,
							amount: null,
							paid: null,
							order: null,
							action: 'delete',
						})
					}
				}
			}
			return temp
		},
	},
	watch: {
	},
	async mounted() {
		await this.load();
	},
	methods: {
		async load(){
			let res;
			try {
				res = await orderRest.read(this.pk)
				this.order = res.data;
				await this.order2data();
			}
			catch (error) {
				console.log(error);
			}
		},

		async order2data(){
			let res;
			res = await this.payments_read_promise();
			this.payments = [];
			this.ids = [];
			res.forEach((v, i)=>  {
				this.payments.push({
					order: i,
					date: v.data.date,
					amount: v.data.amount,
					paid: v.data.paid,
					mode: 'read',
				});
			})
			res.forEach((v, i)=>  {
				this.ids.push({
					id: v.data.id,
				});
			})
		},
		add(){
			let today = new Date()
			let d = Date2String(today);
			this.payments.push({
				order: this.payments.length,
				date: d,
				amount: 0,
				paid: false,
				mode: 'write',
			});
		},
		remove(index){
			this.payments.splice(index,1);
			this.payments.forEach((v, i)=>  {
				v.order = i;
			})
		},
		async operate(action){
			let res;
			if(action=='read'){
				try {
					res = await orderRest.read(this.pk)
					this.order = res.data;
					this.order2data();
				}
				catch (error) {
					console.log(JSON.stringify(error.response));
				}
			}
			else if(action=='update'){
				if(!(this.order.proposal_price==this.total_amount)){
					this.$root.$message.open(-1, '付款金額與方案金額不符，請確認！');
					return -1;
				}

				try {
					res = await this.payments_update_promise();
					res = await orderRest.read(this.pk);
					this.order = res.data;
					this.order2data();
					this.$emit('update', res.data);
					this.$root.$message.open(0, '成功更新付款資訊');
				}
				catch (error) {
					if(error.status){
						this.$root.$message.open(error.status, error.data);
					} else {
						console.log(error);
						this.$root.$message.open(-1, error);
					}
				}
			}

		},
		payments_read_promise(){
			let axiosapis = [];
			this.order.payment_set.forEach((v, i)=>  {
				axiosapis.push(paymentRest.read(v));
			})
			return Promise.all(axiosapis);
		},
		payments_update_promise(){
			let axiosapis = [];
			this.paymentsUpdate.forEach((v, i)=>  {
				if(v.action==='create'){
					axiosapis.push(paymentRest.create(v));
				}
				else if(v.action==='update'){
					axiosapis.push(paymentRest.update(v.id, v));
				}
				else if(v.action==='delete'){
					axiosapis.push(paymentRest.delete(v.id));
				}
			})
			return Promise.all(axiosapis);
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';

	.ctnr_priceAmount {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.ctnr_priceAmount > * {
		margin-left: $spaceS3;
		margin-right: $spaceS3;
	}
</style>



