<template>
	<div :class="['httpStatusCodeCtnr', domClassStatusCodeSeries]">
		<div class="statusCodeDigits">
			<div class="statusCodeDigit statusCodeDigit1">{{ statusCodeDigits[0] }}</div>
			<div class="statusCodeDigit statusCodeDigit2">{{ statusCodeDigits[1] }}</div>
			<div class="statusCodeDigit statusCodeDigit3">{{ statusCodeDigits[2] }}</div>
		</div>
		<div class="title">狀態</div>
	</div>
</template>

<script>
export default {
	props: [
		'statusCode'
	],
	mounted () {
		this.setStatusCodeDigits();
		this.setDomClassStatusCodeSeries();
	},
	data(){
		return {
			statusCodeDigits: null,
			domClassStatusCodeSeries: null,
		}
	},
	methods: {
		setStatusCodeDigits () {
			let statusCodeStr = this.statusCode.toString().split('');
			this.statusCodeDigits = statusCodeStr.map((elm) => {
				return parseInt(elm);
			});
		},
		setDomClassStatusCodeSeries () {
			this.domClassStatusCodeSeries = `statusCode${this.statusCodeDigits[0]}Series`;
		}
	},
}
</script>
<style scoped lang="scss">
	@import '../style/base.scss';
	
	$clorStatusCode1Series: grey;
	$clorStatusCode2Series: green;
	$clorStatusCode3Series: grey;
	$clorStatusCode4Series: brown;
	$clorStatusCode5Series: purple;
	$scaleFactor: 0.8em;
	// .Purpose: Golbal scaling of the whole http status code visual elements.
	// .Value: Use em as units, 1em to set to normal size.

	.httpStatusCodeCtnr {
		display: inline-block;
		text-align: center;
		font-size: $scaleFactor;
		margin: $spaceS3;
	}
	.httpStatusCodeCtnr .statusCodeDigits {
		display: inline-flex;
		justify-content: center;
		align-items: center;
	}
	.httpStatusCodeCtnr .title {
		text-align: center;
		margin-top: 0.5em;
		font-size: 0.9em;
		letter-spacing: 0.2em;
	}
	.statusCodeDigit {
		display: flex;
		box-sizing: content-box;
		margin: 0.3em;
		padding: 0.3em;
		width: 1em;
		height: 1em;
		justify-content: center;
		align-items: center;
		border-radius: 0.2em;
		color: white;
	}
	.statusCodeDigit1 {
		font-size: 2.4em;
		transform: translateY(0.0em) rotate(-13deg);
		margin: 0.1em;
	}
	.statusCodeDigit2 {
		font-size: 0.7em;
		transform: translateY(-1em) rotate(-5deg);
	}
	.statusCodeDigit3 {
		font-size: 0.9em;
		transform: translateY(0em) rotate(5deg);
	}
	.statusCode1Series .statusCodeDigit {
		background-color: $clorStatusCode1Series;
	}
	.statusCode2Series .statusCodeDigit {
		background-color: $clorStatusCode2Series;
	}
	.statusCode3Series .statusCodeDigit {
		background-color: $clorStatusCode3Series;
	}
	.statusCode4Series .statusCodeDigit {
		background-color: $clorStatusCode4Series;
	}
	.statusCode5Series .statusCodeDigit {
		background-color: $clorStatusCode5Series;
	}
	.statusCode1Series .title {
		color: $clorStatusCode1Series;
	}
	.statusCode2Series .title {
		color: $clorStatusCode2Series;
	}
	.statusCode3Series .title {
		color: $clorStatusCode3Series;
	}
	.statusCode4Series .title {
		color: $clorStatusCode4Series;
	}
	.statusCode5Series .title {
		color: $clorStatusCode5Series;
	}
</style>



